const _hoisted_5 = /*#__PURE__*/_createStaticVNode("<div class=\"column is-three-quarters content\" data-v-070f2274><h1 id=\"account-management\" class=\"title is-spaced\" data-v-070f2274>Account Management</h1><h2 id=\"invite-members\" class=\"subtitle pb-2\" data-v-070f2274>Invite Team Members</h2><p data-v-070f2274>You can invite new team members by clicking the <code data-v-070f2274>Copy to clipboard</code> link, which will provide a url which can be used to join your team. Be aware that anyone who receives this link will be able to join your team. </p><p data-v-070f2274>You can also invite new team members by entering their email address in one of the provided boxes and clicking <code data-v-070f2274>Add Teammates</code>. If additional boxes are needed, click the <code data-v-070f2274>+ add more teammates</code> link. </p><p data-v-070f2274>Click <code data-v-070f2274>Accept Invitations</code> to join other teams to which you have been invited. </p><h1 id=\"product-docs\" class=\"title is-spaced\" data-v-070f2274>Product Documentation</h1><h2 id=\"sg-overview\" class=\"subtitle pb-2\" data-v-070f2274>SaaSGlue Framework Overview</h2><h3 data-v-070f2274>Cloud and Agents</h3><p data-v-070f2274>The basic components of the SaaSGlue platform are the cloud service and Agents installed on machines. The machines can be physical, virtual or containers. And they can be located in many types of environments including corporate networks, private clouds and more. </p><p data-v-070f2274>When agents are installed they begin sending a heartbeat to the SaaSGlue cloud. They receive instructions to execute scripts and send the results back to the cloud.</p><h3 data-v-070f2274>Scripts and Jobs</h3><p data-v-070f2274>Scripts can be tied together in Jobs with specific instructions on how they are to be run. </p><p data-v-070f2274>SaaSGlue can run any script and It can even run compiled code. The Agent doesn’t run the scripts. Instead it hands them off to the specified interpreter for execution and collects the output. </p><p data-v-070f2274>Scripts are contained in Steps which also include Arguments and Environment variables which can be passed to the script runtime environment. </p><p data-v-070f2274>Steps are contained in Tasks which can include one or more Steps. All Steps in a Task are executed sequentially on the same machine by the same Agent. </p><p data-v-070f2274>Jobs contain instructions for how a set of Tasks are to be run. They order the Tasks, direct them to the right Agent(s), pass in runtime variables and define conditions when there are alternate Task routes. </p><p data-v-070f2274>Jobs are the central feature that makes SaaSGlue a powerful automation engine. They can reach across all kinds of networks, devices, languages and applications, incorporating them into unified, well-orchestrated workflows. </p><h2 id=\"jobs-structure\" class=\"subtitle pb-2\" data-v-070f2274>The Structure of Jobs</h2><p data-v-070f2274>Jobs are a hierarchical structure of sub-components, including <b data-v-070f2274>Tasks</b>, <b data-v-070f2274>Steps</b> and <b data-v-070f2274>Scripts</b>. </p><h3 data-v-070f2274>Job</h3><p data-v-070f2274>The Job organizes Tasks into a flexible workflow. It contains instructions for how the Job is to be run and stores runtime variables to be consumed during Script execution. </p><h3 data-v-070f2274>Task</h3><p data-v-070f2274>The Task collects steps into a logical unit that run on an Agent. It also stores qualifications for which Agent(s) can run the Task. </p><p data-v-070f2274>Route codes define how the Job moves from Task to Task and sets conditions for alternate routing. </p><h3 data-v-070f2274>Step</h3><p data-v-070f2274>The Steps of a Task are executed in a set sequence. The Step object stores its order in the sequence and environment variables and command line arguments to be passed to Script execution. It can also store a specific command to use to execute the Script. This would override the default interpreter based on the Script type. For example, a command value of <code data-v-070f2274>/usr/bin/python2.7</code> for a Step running a python Script would result in the Script being executed with the interpreter located at <code data-v-070f2274>/usr/bin/python2.7</code> on the machine where the Step runs. </p><h3 data-v-070f2274>Script</h3><p data-v-070f2274>The Script is a SaaSGlue object which contains the code that is to be executed by the interpreter and the type of interpreter to use to run the script. </p><h2 id=\"job-definition\" class=\"subtitle pb-2\" data-v-070f2274>Job Definition</h2><p data-v-070f2274>Job Definitions (JobDefs) are structured workflow templates that coordinate the execution of one or more Tasks to accomplish a goal. For example, a <code data-v-070f2274>JobDef</code> could consist of a single Task that watches for new files in a specific location. When a new file is discovered, it could launch a new Job based on another <code data-v-070f2274>JobDef</code> with a series of Tasks that constitute a data pipeline for processing the file. </p><p data-v-070f2274>JobDefs are normally designed using the SaaSGlue web application. However, they can also be created dynamically using the SaaSGlue API. </p><p data-v-070f2274>JobDefs are used to create Jobs, which are runtime implementations of JobDefs. Creating a Job from a <code data-v-070f2274>JobDef</code> can be accomplished in three ways: </p><ul data-v-070f2274><li data-v-070f2274>Schedule a <code data-v-070f2274>JobDef</code> using the SaaSGlue web application or the SaaSGlue API. When the Schedule is triggered, the <code data-v-070f2274>JobDef</code> will be used as a template to create and run a Job. A scheduled Job may be triggered but not run immediately or at all based on certain <code data-v-070f2274>JobDef</code> attributes.</li><li data-v-070f2274>A Job can be launched from a <code data-v-070f2274>JobDef</code> manually using the SaaSGlue API or using the SaaSGlue web application.</li><li data-v-070f2274>A <code data-v-070f2274>JobDef</code> can be defined in <code data-v-070f2274>JSON</code> and sent to the SaaSGlue API in an <code data-v-070f2274>Http POST</code>.</li></ul><h3 data-v-070f2274>Routes</h3><p data-v-070f2274>The <code data-v-070f2274>JobDef</code> ties Tasks together with routes. Routes include two components, the task name and the route pattern. The route pattern can be any valid javascript regular expression. For example, a route pattern of <code data-v-070f2274>^(?!fail$).*$</code> would match any route code other than <code data-v-070f2274>fail</code>. For a route to be satisfied, a task must be completed with a route code matching the route pattern. </p><p data-v-070f2274>By default, tasks that complete successfully (all steps in the task complete with <code data-v-070f2274>exit code 0</code>) have a blank route code. Tasks that fail (a step in the task completes with non-zero exit code) have route code <code data-v-070f2274>fail</code>. Tasks that are interrupted (either by <code data-v-070f2274>CTRL+C</code> or canceled using SaaSGlue) have route code <code data-v-070f2274>interrupt</code>. To set a custom route code, print a string to stdout following the pattern <code data-v-070f2274>@sgo{&quot;route&quot;: &quot;[route name]&quot;}</code> from a script. If multiple scripts within a task set a custom route code, the last one will be used. </p><p data-v-070f2274>SaaSGlue supports two types of task routing – <b data-v-070f2274>inbound</b> and <b data-v-070f2274>outbound</b> routes. Inbound routes require all routes to be satisfied before the target task is executed. For example, if we have inbound routes from <code data-v-070f2274>task1</code> with route pattern <code data-v-070f2274>.*</code> and <code data-v-070f2274>task2</code> with route pattern <code data-v-070f2274>.*</code> to <code data-v-070f2274>task3</code>, both routes must be satisfied for <code data-v-070f2274>task3</code> to execute. </p><p data-v-070f2274>Outbound routes cause the target task to be executed every time the outbound route is satisfied. For example, if we have outbound routes from <code data-v-070f2274>task1</code> with route pattern <code data-v-070f2274>.*</code> to <code data-v-070f2274>task3</code> and from <code data-v-070f2274>task2</code> with route pattern <code data-v-070f2274>.*</code> to <code data-v-070f2274>task3</code>, then every time the <code data-v-070f2274>task1</code> route is satisfied, <code data-v-070f2274>task3</code> will be executed, and every time the <code data-v-070f2274>task2</code> route is satisfied, <code data-v-070f2274>task3</code> will be executed. An example use case would be a general failed task handler. You could implement this by creating an outbound route from every other task in the job to a <code data-v-070f2274>failed task handler</code> task. This scenario is facilitated in the web application by the <code data-v-070f2274>Route All To [task name]</code> button in the Workflow Designer view. </p><div class=\"notification is-info is-light\" data-v-070f2274> Task routes can be defined using the SaaSGlue API for JobDefs and Jobs and using the SaaSGlue web application. There are a few settings in the job definition that define conditions for how jobs are to be run. </div><ul data-v-070f2274><li data-v-070f2274><b data-v-070f2274>Max Instances</b> – defines how many instances of a job can run concurrently.</li><li data-v-070f2274><b data-v-070f2274>Misfire Grace Time</b> – defines how long a job can wait past a scheduled start time before it is cancelled. This setting applies only to jobs executed by a schedule.</li><li data-v-070f2274><b data-v-070f2274>Coalesce</b> – directs SaaSGlue to skip all but one job when there are multiple jobs queued up. This setting is useful for scenarios where an agent has gone offline and missed the start time for several scheduled jobs. It may be desirable to run only one instance and skip the rest.</li><li data-v-070f2274><b data-v-070f2274>Pause on Failed Job</b> – if <code data-v-070f2274>true</code>, when a Job created from the <code data-v-070f2274>JobDef</code> fails, the <code data-v-070f2274>JobDef</code> will be <b data-v-070f2274>paused</b> until it is manually <b data-v-070f2274>resumed</b> (using the SaaSGlue cloud interface or the <a href=\"/docs#jobdef\" data-v-070f2274>API</a>) or all failed Tasks are either canceled or completed successfully. This facilitates certain workflow scenarios, for example where all Jobs created from a <code data-v-070f2274>JobDef</code> must complete in order.</li></ul><div class=\"notification is-info is-light\" data-v-070f2274> Job Definitions can include runtime variables. </div><h2 id=\"task-definition\" class=\"subtitle pb-2\" data-v-070f2274>Task Definition</h2><p data-v-070f2274>Each <code data-v-070f2274>Task</code> can have one or more <code data-v-070f2274>Steps</code> that must be run in a set sequence. </p><p data-v-070f2274>Tasks can target one or multiple Agents. If a <code data-v-070f2274>Task</code> targets multiple Agents each Agent will run separate instances of the <code data-v-070f2274>Task</code> in parallel. Once an Agent begins processing a <code data-v-070f2274>Task</code>, all <code data-v-070f2274>Steps</code> in the <code data-v-070f2274>Task</code> will be completed on that Agent machine. If a <code data-v-070f2274>Task</code> fails or is interrupted it can be restarted, although it may not be restarted on the machine where it was previously running. </p><p data-v-070f2274>The <code data-v-070f2274>Steps</code> of a specific Task instance must all be completed by the same Agent because there are some sequences of events that cannot be shared across multiple machines. Here is an example. </p><ol data-v-070f2274><li data-v-070f2274>Install Java</li><li data-v-070f2274>Run a Java application</li></ol><p data-v-070f2274>If step 1 runs on one machine and step 2 runs on a different machine where Java has not been installed, step2 will fail. </p><p data-v-070f2274>The <code data-v-070f2274>Job</code> designer must understand the logical boundaries of each discrete part of the Job and design accordingly. </p><p data-v-070f2274>Dependencies on prior <code data-v-070f2274>Task</code> outcomes are stored in the <code data-v-070f2274>Task</code> definition <b data-v-070f2274>inbound</b> routes. They determine whether the <code data-v-070f2274>Job</code> route will route through a given <code data-v-070f2274>Task</code> or not. Downstream dependencies are stored in the <code data-v-070f2274>Task</code> definition <b data-v-070f2274>outbound</b> routes. They determine which Tasks will be started when this <code data-v-070f2274>Task</code> completes. See the JobDef routing description for details. </p><p data-v-070f2274>Task definitions store the qualifications for determining which Agent(s) can run the <code data-v-070f2274>Task</code>. Those qualifications are as follows: </p><ul data-v-070f2274><li data-v-070f2274>Any active Agent</li><li data-v-070f2274>Any active Agent with tags as specified in the Task definition</li><li data-v-070f2274>All active Agents</li><li data-v-070f2274>All active Agents with tags as specified in the Task definition</li><li data-v-070f2274>A specific Agent</li></ul><p data-v-070f2274>Tags are user defined and must be formatted in key value pairs, such as <code data-v-070f2274>Linux=True</code>. <code data-v-070f2274>Task</code> Tags can be defined using the SaaSGlue web application or the SaaSGlue API. </p><p data-v-070f2274>If a <code data-v-070f2274>Task</code> needs to run on a specific agent, use the Job Designer in the SaaSGlue web application or the SaaSGlue API to specify which agent should run the <code data-v-070f2274>Task</code>. It is also possible to dynamically determine the target agent at runtime using runtime variables. Simply set the target agent to <code data-v-070f2274>@sgg(&quot;[runtime variable key]&quot;)</code> and the agent id referenced by <code data-v-070f2274>[runtime variable key]</code> will be used to execute the <code data-v-070f2274>Task</code>. </p><h2 id=\"step-definition\" class=\"subtitle pb-2\" data-v-070f2274>Step Definition</h2><p data-v-070f2274>Steps are contained within a <code data-v-070f2274>Task</code> and must be run in a set sequence. The <code data-v-070f2274>Step</code> definition stores its order in the sequence. </p><p data-v-070f2274>Each <code data-v-070f2274>Step</code> can have only one <code data-v-070f2274>Script</code> object. The <code data-v-070f2274>Step</code> definition can store arguments or environment variables which are needed to run the script. <code data-v-070f2274>Step</code> arguments and environment variables can be set dynamically at runtime using Runtime Variables. </p><p data-v-070f2274>The command attribute can be used to specify the exact interpreter to use to execute the script for Steps created from this <code data-v-070f2274>StepDef</code>. It can also be used to set arguments that should be passed to that interpreter. For example, to pass a class path to the scala interpreter. If not specified, the default interpreter for the script type will be used to execute the script. </p><p data-v-070f2274>For example, if you have a script of type <code data-v-070f2274>python</code> and the <code data-v-070f2274>command</code> attribute is not set, then the default python interpreter on the machine where the <code data-v-070f2274>Step</code> runs will be used to execute the script. If the command attribute is set to <code data-v-070f2274>/usr/bin/python2.7</code>, then the interpreter in that location on the Agent machine will be used to run the script. The interpreter specified by the command attribute must exist on the machine on which the <code data-v-070f2274>Step</code> runs. </p><p data-v-070f2274>The designer can give the <code data-v-070f2274>Step</code> definition a name to make it more convenient to work with. </p><h2 id=\"script-definition\" class=\"subtitle pb-2\" data-v-070f2274>Script Definition</h2><p data-v-070f2274>The <code data-v-070f2274>Script</code> object stores the <code data-v-070f2274>Script</code> code. </p><p data-v-070f2274>It also stores useful information about the <code data-v-070f2274>Script</code> including the <code data-v-070f2274>Script</code> language, the original author and who last edited it. </p><p data-v-070f2274>SaaSGlue can be used to execute any <code data-v-070f2274>Script</code>. The <code data-v-070f2274>Script</code> type attribute is added to the <code data-v-070f2274>Script</code> object to make it easier to work with common scripting languages. For example, the SaaSGlue cloud interface <code data-v-070f2274>Script</code> editor provides cues based on the script language. To use a script language not defined in SaaSGlue use <code data-v-070f2274>SH</code> if the <code data-v-070f2274>Script</code> will run on <code data-v-070f2274>Linux, Linux based or Mac</code> operating systems and <code data-v-070f2274>CMD for Windows</code> and then define the <code data-v-070f2274>Command</code> attribute either in the StepDef or when executing the <code data-v-070f2274>Script</code> from the Console. </p><p data-v-070f2274>The following script types are predefined in SaaSGlue:</p><ul data-v-070f2274><li data-v-070f2274>0 = PYTHON</li><li data-v-070f2274>1 = NODE</li><li data-v-070f2274>2 = SH</li><li data-v-070f2274>3 = CMD</li><li data-v-070f2274>4 = RUBY</li><li data-v-070f2274>5 = LUA</li><li data-v-070f2274>6 = PERL</li><li data-v-070f2274>7 = PHP</li><li data-v-070f2274>8 = POWERSHELL</li></ul><div class=\"notification is-info is-light\" data-v-070f2274> Scripts can be set to be editable within the team. All Scripts are viewable within the team. </div><div class=\"notification is-warning is-light\" data-v-070f2274> The author can give the Script a name to make it more convenient to work with. The name must be unique within the team. </div><h2 id=\"script-injection\" class=\"subtitle pb-2\" data-v-070f2274>Script Injection</h2><p data-v-070f2274>Scripts defined in SaaSGlue can be injected into other scripts using the following syntax <code data-v-070f2274>@sgs(&quot;[script name]&quot;)</code></p><p data-v-070f2274>This line entered in a script will result in the script defined by <code data-v-070f2274>[script name]</code> being injected into the host script immediately before executing the script. </p><p data-v-070f2274>Scripts can be injected recursively meaning that <code data-v-070f2274>script 1</code> can reference <code data-v-070f2274>script 2</code> which can reference <code data-v-070f2274>script 3</code>, etc. </p><p data-v-070f2274>However, the <code data-v-070f2274>[script name]</code> itself cannot be a variable – it must be the name of a script defined in your SaaSGlue team. </p><h2 id=\"runtime-variables\" class=\"subtitle pb-2\" data-v-070f2274>Runtime Variables</h2><p data-v-070f2274>Runtime Variables are <b data-v-070f2274>key-value</b> pairs. They provide a mechanism for dynamically injecting variable values in <code data-v-070f2274>Script</code> code, Arguments and Environment Variables of a Task Step when it is executed by a SaaSGlue Agent. Runtime variables can be defined at design time on the Team level and in JobDefs, and when a <code data-v-070f2274>Job</code> is created and they can also be generated dynamically during <code data-v-070f2274>Script</code> execution. </p><h3 data-v-070f2274>How to define</h3><h4 data-v-070f2274>Team scoped runtime variables</h4><p data-v-070f2274>Runtime Variables can be defined on the Team level using the SaaSGlue cloud interface or the SaaSGlue API. </p><h4 data-v-070f2274>JobDef scoped runtimes variables</h4><p data-v-070f2274>Runtime Variables can be defined in JobDefs using the SaaSGlue cloud interface or the SaaSGlue API. </p><h4 data-v-070f2274>Job scoped runtime variables</h4><p data-v-070f2274>Runtime Variables can be defined in a <code data-v-070f2274>Job</code> when the <code data-v-070f2274>Job</code> is created. When a <code data-v-070f2274>Job</code> is created using the SaaSGlue API the runtime variables can be included in the <code data-v-070f2274>Http POST</code> request. When a <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JobDef</code> in the SaaSGlue cloud UI the runtime variables can be entered in the <code data-v-070f2274>Job</code> creation dialog. </p><h4 data-v-070f2274>Dynamically generated runtime variables</h4><p data-v-070f2274>Runtime Variables can be generated dynamically during <code data-v-070f2274>Script</code> execution by printing a properly formatted string to stdout. The format is <code data-v-070f2274>@sgo{&quot;[variable name]&quot;: &quot;[variable value]&quot;}.</code></p><p data-v-070f2274>For example, the following line of python script code would dynamically create or modify a runtime variable with name <code data-v-070f2274>myKey</code> and value <code data-v-070f2274>&quot;myValue&quot;</code> when executed by a SaaSGlue Agent <code data-v-070f2274>print &#39;@sgg{&quot;myKey&quot;: &quot;myValue&quot;}&#39;</code>. </p><p data-v-070f2274>The <code data-v-070f2274>&quot;myKey&quot;</code> runtime variable would now be defined as <code data-v-070f2274>&quot;myValue&quot;</code> for subsequent Steps in the <code data-v-070f2274>Task</code> being executed and for subsequent Tasks in the <code data-v-070f2274>Job</code> workflow. </p><p data-v-070f2274>SaaSGlue reserves the key word <code data-v-070f2274>route</code> to determine route-based pathing between Tasks in a Job workflow. So the following output <code data-v-070f2274>@sgg{&quot;route&quot;: &quot;ok&quot;}</code> would not create a runtime variable named <code data-v-070f2274>route</code>. Instead it would be used for routing from the executing <code data-v-070f2274>Task</code> in the <code data-v-070f2274>Job</code> workflow. </p><p data-v-070f2274>If a runtime variable is defined on multiple levels, each successively granular level overrides the previous. For example, if we have a variable named <code data-v-070f2274>key</code> defined on the Team level with a value of <code data-v-070f2274>team_value</code> and in a <code data-v-070f2274>JobDef</code> with a value of <code data-v-070f2274>jobdef_value</code>, the <code data-v-070f2274>JobDef</code> value will override the Team value. If the variable is defined in a <code data-v-070f2274>Job</code> created from the <code data-v-070f2274>JobDef</code> with a value of <code data-v-070f2274>job_value</code>, the <code data-v-070f2274>Job</code> value will override the <code data-v-070f2274>JobDef</code> value. If within that <code data-v-070f2274>Job</code> a <code data-v-070f2274>Script</code> runs which prints the following string to stdout <code data-v-070f2274>sgo{&quot;key&quot;: &quot;script_value&quot;}</code>, the <code data-v-070f2274>Script</code> value will override the <code data-v-070f2274>Job</code> value and any subsequent reference to <code data-v-070f2274>key</code> will have the value <code data-v-070f2274>script_value</code>. A subsequent <code data-v-070f2274>Script</code> could override the value again. </p><h3 data-v-070f2274>How to use</h3><p data-v-070f2274>Runtime Variables values can be dynamically injected into the <code data-v-070f2274>Script</code> code, Arguments and Environment Variables of a <code data-v-070f2274>Step</code>. Immediately before running a <code data-v-070f2274>Task Step</code>, the Agent will search the <code data-v-070f2274>Step Script</code> code Arguments for strings following the pattern <code data-v-070f2274>sgg(&quot;[key]&quot;)</code> and will replace them with the current value of the runtime variable with the name <code data-v-070f2274>[key]</code>. If no runtime variable with the name <code data-v-070f2274>[key]</code> exists, <code data-v-070f2274>null</code> will be injected instead. </p><h4 data-v-070f2274>Step Script code runtime variable injection</h4><p data-v-070f2274>For example, if we have a python script with the following line of code </p><p data-v-070f2274><code data-v-070f2274>print(&#39;the value of &quot;myKey&quot; is &quot;@sgg(&quot;myKey&quot;)&quot;&#39;)</code></p><p data-v-070f2274>and a runtime variable <code data-v-070f2274>myKey</code> with value <code data-v-070f2274>myValue</code>, the script will produce the following output </p><p data-v-070f2274><code data-v-070f2274>the value of &quot;myKey&quot; is &quot;myValue&quot;</code>.</p><h4 data-v-070f2274>Step Arguments runtime variable injection</h4><p data-v-070f2274>If we have a Step Argument with value <code data-v-070f2274>@sgg(&quot;myKey&quot;) -out myFile.txt&quot;</code> , and the runtime value of <code data-v-070f2274>myKey</code> is <code data-v-070f2274>myValue</code> the <code data-v-070f2274>Script</code> code for the <code data-v-070f2274>Step</code> will be called with arguments <code data-v-070f2274>&quot;myValue -out myFile.txt&quot;</code>. </p><h4 data-v-070f2274>Step Environment Variables runtime variable injection</h4><p data-v-070f2274>Runtime Variable injection for Step Environment Variables works slightly differently. Step Environment Variable keys are compared to the current runtime variables for matching keys. If a match is found, the runtime variable value will be used for the environment variable value when running the Script. </p><p data-v-070f2274>For example, if we have a Step Environment Variable <code data-v-070f2274>myKey</code> and a runtime variable <code data-v-070f2274>myKey</code> with value <code data-v-070f2274>myValue</code>, then the runtime script execution environment will have an environment variable <code data-v-070f2274>myKey=myValue</code>. </p><h4 data-v-070f2274>Design time configuration</h4><p data-v-070f2274>A <code data-v-070f2274>StepDef</code> can be configured so that Steps created from the <code data-v-070f2274>StepDef</code> utilize runtime variables in arguments and/or environment variables using the SaaSGlue API or the SaaSGlue cloud interface. </p><div class=\"notification is-info is-light\" data-v-070f2274> Runtime variable injection can also be utilized when running Scripts using the SaaSGlue cloud interface Console. </div><h4 data-v-070f2274>Dynamically select Task target agent id</h4><p data-v-070f2274>Runtime variables can be used to dynamically determine the target agent for Tasks that target a single specific agent at runtime. For example, if you have a runtime variable named <code data-v-070f2274>agentId</code> and the value is an actual agent id, and the Task target agent id is <code data-v-070f2274>@sgg(&quot;agentId&quot;)</code>, the task with the id matching the runtime variable value will be used to run the <code data-v-070f2274>Task</code>. </p><h4 data-v-070f2274>Save state with runtime variables</h4><p data-v-070f2274>As Scripts are executing the corresponding stdout is monitored in real-time and searched for strings following the <code data-v-070f2274>@sgo</code> runtime variable generation pattern. When runtime variables are created/modified, the new values are sent to the SaaSGlue cloud. If a <code data-v-070f2274>Task</code> fails and is restarted, the most recent runtime variable values will be delivered with the <code data-v-070f2274>Task</code> to the Agent where the <code data-v-070f2274>Task</code> resumes execution. Consequently, Scripts can be designed to utilize the most recent value of the runtime variable. </p><p data-v-070f2274>For example, if a <code data-v-070f2274>Script</code> is designed to iterate through records in a file, the line number could be printed to stdout with a string like <code data-v-070f2274>@sgo{&quot;line_num&quot;: &quot;10&quot;}</code> which would send the runtime variable <code data-v-070f2274>line_num</code> with a value of <code data-v-070f2274>10</code> to the SaaSGlue cloud. If the <code data-v-070f2274>Task</code> failed on line <code data-v-070f2274>&quot;11&quot;</code> and the <code data-v-070f2274>Task</code> is restarted, the <code data-v-070f2274>Script</code> could resume processing the file on the line after <code data-v-070f2274>@sgg(&quot;line_num&quot;)</code> which would now have a value of <code data-v-070f2274>10</code>. </p><div class=\"notification is-info is-light\" data-v-070f2274> This behavior does not apply to Tasks that target multiple agents. </div><h2 id=\"job-schedule\" class=\"subtitle pb-2\" data-v-070f2274>Job Schedule</h2><p data-v-070f2274>Schedules can be created for Job Definitions (JobDefs) to automatically trigger Jobs to run. A <code data-v-070f2274>JobDef</code> can have more than one Schedule but a Schedule can be linked to only one <code data-v-070f2274>JobDef</code>. </p><p data-v-070f2274>There are three types of schedules.</p><ul data-v-070f2274><li data-v-070f2274>Date</li><li data-v-070f2274>Cron</li><li data-v-070f2274>Interval</li></ul><h3 data-v-070f2274>RunDate</h3><p data-v-070f2274>Date is the simplest schedule. It stores a single date and time for when the job will be triggered to run. </p><h3 data-v-070f2274>Cron</h3><p data-v-070f2274>Cron resembles the <code data-v-070f2274>Cron</code> utility found in most UNIX-like operating systems. The following fields are available to define a cron schedule. </p><ul data-v-070f2274><li data-v-070f2274><b data-v-070f2274>Year</b> 4-digit year</li><li data-v-070f2274><b data-v-070f2274>Month</b> 1-12</li><li data-v-070f2274><b data-v-070f2274>Day</b> day of the month (1-31)</li><li data-v-070f2274><b data-v-070f2274>Week</b> ISO week (1-53)</li><li data-v-070f2274><b data-v-070f2274>Day of Week</b> number or name of day (0-6 or mon, tue, wed, thu, fri, sat, sun)</li><li data-v-070f2274><b data-v-070f2274>Hour</b> 0-23</li><li data-v-070f2274><b data-v-070f2274>Minute</b> 0-59</li><li data-v-070f2274><b data-v-070f2274>Second</b> 0-59</li><li data-v-070f2274><b data-v-070f2274>Start Date</b> earliest possible date/time to trigger the job</li><li data-v-070f2274><b data-v-070f2274>End Date</b> latest possible date/time to trigger the job</li><li data-v-070f2274><b data-v-070f2274>Jitter</b> advance or delay the job execution by jitter seconds</li></ul><p data-v-070f2274>A cron schedule will trigger when current time matches all specified time constraints. Unlike with crontab expressions, you can omit fields that you don’t need. </p><h3 data-v-070f2274>Interval</h3><p data-v-070f2274>Interval specifies a set amount of time that must lapse before a job will be triggered. The following fields are available to define an interval schedule. </p><ul data-v-070f2274><li data-v-070f2274><b data-v-070f2274>Weeks</b> integer for the total number of weeks</li><li data-v-070f2274><b data-v-070f2274>Days</b> integer for the total number of days</li><li data-v-070f2274><b data-v-070f2274>Hours </b> integer for the total number of hours</li><li data-v-070f2274><b data-v-070f2274>Seconds</b> integer for the total number of seconds</li><li data-v-070f2274><b data-v-070f2274>Start Date</b> earliest possible date/time to trigger the job</li><li data-v-070f2274><b data-v-070f2274>End Date</b> latest possible date/time to trigger the job</li><li data-v-070f2274><b data-v-070f2274>Jitter</b> advance or delay the job execution by jitter seconds</li></ul><p data-v-070f2274>An interval schedule will trigger a job when the time interval has been reached from the previous trigger time. </p><p data-v-070f2274>The <code data-v-070f2274>Is Active</code> box must be checked to activate the schedule. </p><p data-v-070f2274>The designer can give the schedule a name to make it more convenient to work with. The designer displays the last run date and the next run date for active schedules. </p><h2 id=\"service-tasks\" class=\"subtitle pb-2\" data-v-070f2274>Service Tasks</h2><p data-v-070f2274>Have you written <b data-v-070f2274>services</b> for Windows? Linux? Mac? Then you know that it can be difficult and that the process works differently in each environment. Occasionally work-arounds are implemented for actual services such as cron or the Windows Task Scheduler. If the machine running the service shuts down, the service is stopped. </p><p data-v-070f2274>SaaSGlue makes it easy to create a service and deploy it to any operating system. To run a task as a service, simply set the <code data-v-070f2274>autoRestart</code> property of the task to <code data-v-070f2274>true</code> if using the SaaSGlue API. If using the SaaSGlue cloud UI, in the <b data-v-070f2274>Job Designer -&gt; Task</b> editor view, check the <code data-v-070f2274>Auto restart</code> checkbox. The task can target <code data-v-070f2274>any qualified agent</code> or a specific agent. If the agent running an <code data-v-070f2274>autoRestart</code> task is stopped or is inactive for more than <strong data-v-070f2274>65 seconds</strong>, the task will automatically be restarted on another qualified agent. If no qualified agent is available, the task status will be set to <code data-v-070f2274>Failed</code>. The containing job can be configured to send a notification if this occurs. If an agent running an <code data-v-070f2274>autoRestart</code> task goes offline and then comes back online and the task is still running on that agent and has also been restarted on a new agent, the instance on the agent that went offline will be canceled. </p><p data-v-070f2274>Auto restart tasks can have multiple steps. The script for the last step in the task should be coded so that it will not exit unless and until you want the service to be stopped. Auto restart tasks that exit normally or crash are not restarted automatically. However, if an auto restart task is interrupted by <code data-v-070f2274>CTRL + C</code> or a <code data-v-070f2274>SIGTERM/SIGKILL</code> signal it will be automatically restarted. Consequently if the machine running an auto restart task is shut down, the task will be automatically restarted. </p><h1 id=\"endpoint\" class=\"title is-spaced\" data-v-070f2274>Endpoint / Object Reference</h1><h2 id=\"agent\" class=\"subtitle pb-2\" data-v-070f2274>Agent</h2><p data-v-070f2274>The SaaSGlue Agent is the vehicle for executing Scripts received from the SaaSGlue cloud. The Agent can be installed on any supported operating system. When an Agent starts up it reports to the SaaSGlue cloud which tracks information about the Agent including the attributes described below. A few of the attributes can be modified by users. </p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>machineId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>ipAddress</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>createDate</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>tags</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>numActiveTasks</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>lastHeartbeatTime</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>offline</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>sysInfo</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>cron</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>propertyOverrides</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>SaaSGlue automatically assigns an Agent ID when a new agent is installed and calls back to the SaaSGlue cloud. Users can discover the ID in the Agent Monitor or through the API. Tasks can be routed to a specific machine using the Agent ID.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the TaskDef is.</p><h4 data-v-070f2274><b data-v-070f2274>machineId</b></h4><p data-v-070f2274>The hostname of the physical or virtual machine or container where the Agent is running.</p><p data-v-070f2274>Every machine connected to a SaaSGlue account must have a host name that is unique to the Org. The machineId will be reported in every Agent heartbeat. If the hostname changes then the Agent will be assigned a new ID.</p><h4 data-v-070f2274><b data-v-070f2274>ipAddress</b></h4><p data-v-070f2274>The ipAddress attribute stores the IP Address of the machine where the Agent is installed.</p><p data-v-070f2274>The IP Address will be reported with each heartbeat. If the IP address of the machine is changed the ipAddress attribute will be updated but the Agent will not be assigned a new ID.</p><h4 data-v-070f2274><b data-v-070f2274>createDate</b></h4><p data-v-070f2274>The date and time an Agent first runs on a machine.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The name attribute defaults to the machineId (hostname). However, it can be modified to create a user-defined name.</p><h4 data-v-070f2274><b data-v-070f2274>tags</b></h4><p data-v-070f2274>Tags assigned to an Agent. Up to ten tags can be assigned to an Agent.</p><p data-v-070f2274>Tags are user defined. They are used in the Job Designer and Console for Task routing.</p><h4 data-v-070f2274><b data-v-070f2274>numActiveTasks</b></h4><p data-v-070f2274>The number of Tasks currently being executed by this Agent.</p><h4 data-v-070f2274><b data-v-070f2274>lastHeartbeatTime</b></h4><p data-v-070f2274>The last date and time SaaSGlue received a heartbeat from the Agent.</p><h4 data-v-070f2274><b data-v-070f2274>offline</b></h4><p data-v-070f2274>If SaaSGlue has not received a heartbeat from an Agent for more than 5 minutes the offline attribute is set to &quot;true&quot;. If the Agent begins sending a heartbeat again the offline attribute will be reset to “false”.</p><h4 data-v-070f2274><b data-v-070f2274>sysInfo</b></h4><p data-v-070f2274>System information pertaining to the machine where the Agent is installed. The following information (if available) will be reported.</p><p data-v-070f2274> Top 10 running processes (ordered by CPU usage): <ul data-v-070f2274><li data-v-070f2274>Installed OS details</li><li data-v-070f2274>System date/time</li><li data-v-070f2274>CPU current speed</li><li data-v-070f2274>CPU temperature</li><li data-v-070f2274>Current load information</li><li data-v-070f2274>File system information</li><li data-v-070f2274>File system performance statistics</li><li data-v-070f2274>Installed user accounts</li><li data-v-070f2274>Memory capacity/usage information</li><li data-v-070f2274>Battery capacity/usage information</li><li data-v-070f2274>Network connections</li><li data-v-070f2274>Current internet latency</li><li data-v-070f2274>Network statistics</li><li data-v-070f2274>Disks IO</li></ul></p><p data-v-070f2274>The Agent will report system information when it is first installed. It can be configured to report updated information with each heartbeat.</p><h4 data-v-070f2274><b data-v-070f2274>cron</b></h4><p data-v-070f2274>Cron jobs that reside on the machine where the Agent is installed. These jobs can be uploaded to SaaSGlue where they can be centrally managed and orchestrated.</p><h4 data-v-070f2274><b data-v-070f2274>propertyOverrides</b></h4><p data-v-070f2274>Agent settings that can be overridden by users including the following:</p><ul data-v-070f2274><li data-v-070f2274><code data-v-070f2274>maxActiveTasks</code> limits the number of tasks an Agent can run in parallel. If more than <code data-v-070f2274>maxActiveTasks</code> are routed to an Agent, Tasks in excess of <code data-v-070f2274>maxActiveTasks</code> will be queued until running Tasks are completed. If a Task is queued for more than the Task TTL, the Task will be routed as “failed”.</li><li data-v-070f2274><code data-v-070f2274>inactivePeriodWaitTime</code> sets the max amount of time an Agent will remain idle before it runs a specified Task. This is useful for some Tasks, such as shutting down an idle EC2 instance.</li><li data-v-070f2274><code data-v-070f2274>inactiveAgentTask</code> is the Task that will be run if and when the <code data-v-070f2274>inactivePeriodWait</code> time is exceeded. The Task must be JSON formatted and adhere to the Task specification.</li><li data-v-070f2274><code data-v-070f2274>handleGeneralTasks</code> can exclude an Agent from running Tasks that are marked as &quot;run on any machine&quot; or &quot;run on all machines&quot; if this attribute is set to “false”.</li><li data-v-070f2274><code data-v-070f2274>trackSysInfo</code> reports updated system information to the SaaSGlue cloud with each heartbeat.</li></ul><h3 data-v-070f2274>Actions</h3><h4 data-v-070f2274>download</h4><p data-v-070f2274>Download the Agent install from SaaSGlue - <code data-v-070f2274>agentDownload/agentstub/:platform/:arch</code></p><p data-v-070f2274><code data-v-070f2274>:platform</code>(required) - must be one of: <code data-v-070f2274>linux, win, macos, alpine, freebsd</code></p><p data-v-070f2274><code data-v-070f2274>:arch</code>(optional) - must be one of: <code data-v-070f2274>x64, x86, armv6, armv7</code></p><p data-v-070f2274>Not all platform/arch combinations are valid.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Get all Agents objects in the Org</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/agent&#39; --header &#39;Content-Type: application/json&#39; --header &#39;Auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;numActiveTasks&quot;:0,\n        &quot;lastHeartbeatTime&quot;:1588863480826,\n        &quot;offline&quot;:false,\n        &quot;cron&quot;:&quot;&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;machineId&quot;:&quot;[Machine ID]&quot;,\n        &quot;ipAddress&quot;:&quot;[IP Address]&quot;,\n        &quot;name&quot;:&quot;[Agent Name]&quot;,\n        &quot;propertyOverrides&quot;:{\n            &quot;trackSysInfo&quot;:false,\n            &quot;maxActiveTasks&quot;:20\n        },\n        &quot;sysInfo&quot;:{},\n        &quot;createDate&quot;:&quot;[Date]&quot;,\n        &quot;tags&quot;:{},\n        &quot;id&quot;:&quot;[Agent ID]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Agent&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Update an Agent property</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request PUT &#39;https://saasglue.com/api/v0/agent/properties/[agentId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;trackSysInfo&quot;: true }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;numActiveTasks&quot;:0,\n        &quot;lastHeartbeatTime&quot;:1588863480826,\n        &quot;offline&quot;:false,\n        &quot;cron&quot;:&quot;&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;machineId&quot;:&quot;[Machine ID]&quot;,\n        &quot;ipAddress&quot;:&quot;[IP Address]&quot;,\n        &quot;name&quot;:&quot;[Agent Name]&quot;,\n        &quot;propertyOverrides&quot;:{\n            &quot;trackSysInfo&quot;:true,\n            &quot;maxActiveTasks&quot;:20\n        },\n        &quot;sysInfo&quot;:{},\n        &quot;createDate&quot;:&quot;[Date]]&quot;,\n        &quot;tags&quot;:{},\n        &quot;id&quot;:&quot;[Agent ID]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Agent&quot;\n    }\n}\n                    </pre><h2 id=\"artifact\" class=\"subtitle pb-2\" data-v-070f2274>Artifact</h2><p data-v-070f2274>Artifacts are files that can be uploaded to SaaSGlue to be used in the execution of Jobs and Tasks.</p><p data-v-070f2274>An <code data-v-070f2274>Http GET</code> request will return a secure URL which can be used to download an artifact. <code data-v-070f2274>Http PUT</code> and <code data-v-070f2274>POST</code> requests will return a secure URL which can be used to upload an artifact.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th><th data-v-070f2274>DELETE</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>prefix</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new Artifact object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>TaskDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>Artifact Name is the name of the file that is uploaded. The combination of Prefix and Name attributes for an Artifact must be unique within an Org.</p><h4 data-v-070f2274><b data-v-070f2274>prefix</b></h4><p data-v-070f2274>Prefix is the path name where an artifact is stored in SaaSGlue. The path can include one or more <code data-v-070f2274>/</code> delimited folder names - e.g. <code data-v-070f2274>prod/loader/</code>.</p><h3 data-v-070f2274>Sample code</h3><h4 data-v-070f2274>Upload an artifact</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/artifact&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;_teamId&quot;: &quot;[teamId]]&quot;, &quot;prefix&quot;: &quot;[prefix]&quot;, &quot;name&quot;: &quot;[file name]&quot; }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;prefix&quot;:&quot;[prefix]&quot;,\n        &quot;type&quot;:&quot;Artifact&quot;,\n        &quot;url&quot;:&quot;[secure url]&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;name&quot;:&quot;[file name]&quot;,\n        &quot;id&quot;:&quot;[Artifact ID]&quot;,\n        &quot;version&quot;:0\n    }\n}\n                    </pre><p data-v-070f2274>Now use the <code data-v-070f2274>url</code> returned from the previous call to upload the file to SaaSGlue</p><pre data-v-070f2274>curl -v --upload-file [file name] [url] --header &#39;Content-Type: multipart/form-data&#39;</pre><h4 data-v-070f2274>Download an artifact</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/artifact/[ArtifactID]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Now use the “url” returned from the previous call to download the file</p><pre data-v-070f2274>curl [url] &gt; [file name]</pre><h2 id=\"job\" class=\"subtitle pb-2\" data-v-070f2274>Job</h2><p data-v-070f2274>A <code data-v-070f2274>Job</code> is the runtime implementation of a <code data-v-070f2274>JobDef</code>. A <code data-v-070f2274>Job</code> can be created from an existing <code data-v-070f2274>JobDef</code> or from a properly formatted JSON document. Jobs are automatically submitted for execution when they are created.</p><p data-v-070f2274>Jobs can be created from a <code data-v-070f2274>JobDef</code> by</p><ul data-v-070f2274><li data-v-070f2274>scheduling the <code data-v-070f2274>JobDef</code></li><li data-v-070f2274>with the SaaSGlue web application</li><li data-v-070f2274>by submitting a POST to the Job API endpoint</li></ul><p data-v-070f2274>Jobs are created from a JSON document by submitting the JSON document to the Job API endpoint.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobDefId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x1</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x2</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>createdBy</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateCreated</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateScheduled</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateStarted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateCompleted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>status</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>error</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runtimeVars</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runAsService</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><ul data-v-070f2274><li data-v-070f2274>1 Required if <code data-v-070f2274>Job</code> is created from <code data-v-070f2274>JobDef</code> object</li><li data-v-070f2274>2 Required if <code data-v-070f2274>Job</code> is created from API call</li></ul><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new Job object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the Job is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobDefId</b></h4><p data-v-070f2274>The JobDef ID from which the Job was created. It only applies to Jobs created from a JobDef object.</p><h4 data-v-070f2274><b data-v-070f2274>runId</b></h4><p data-v-070f2274>JobDefs have a counter that is incremented each time a Job is created from the JobDef object. The runId attribute is the value of that counter when the Job was created. It can be used to order Jobs created from the same JobDef sequentially. For example, if a given Job object is the third one ever created from a JobDef object, it will have a runId attribute of 3.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The Job name. If the Job is created from a JobDef, the Job name is copied from the JobDef name. If the Job is created from JSON, the name must be included in the JSON document.</p><h4 data-v-070f2274><b data-v-070f2274>createdBy</b></h4><p data-v-070f2274>The id of the user who created the Job. For Jobs created from a JobDef it will be the user who created the JobDef object. For Jobs created from an API call it will be based on the auth token of the user submitting the JSON document.</p><h4 data-v-070f2274><b data-v-070f2274>dateCreated</b></h4><p data-v-070f2274>The date and time the Job was created.</p><h4 data-v-070f2274><b data-v-070f2274>dateScheduled</b></h4><p data-v-070f2274>The date and time a Job is scheduled to run. It only applies to Jobs created from JobDef objects with a Schedule.</p><h4 data-v-070f2274><b data-v-070f2274>dateStarted</b></h4><p data-v-070f2274>The date and time the Job started.</p><h4 data-v-070f2274><b data-v-070f2274>dateCompleted</b></h4><p data-v-070f2274>The date and time the Job completed.</p><h4 data-v-070f2274><b data-v-070f2274>status</b></h4><p data-v-070f2274>The current <code data-v-070f2274>Job</code> status. Here are the possible values.</p><ul data-v-070f2274><li data-v-070f2274>0 = NOT_STARTED</li><li data-v-070f2274>10 = RUNNING</li><li data-v-070f2274>14 = INTERRUPTING</li><li data-v-070f2274>15 = INTERRUPTED</li><li data-v-070f2274>17 = CANCELLING</li><li data-v-070f2274>20 = SUCCEEDED</li><li data-v-070f2274>21 = CANCELLED</li><li data-v-070f2274>22 = FAILED</li><li data-v-070f2274>23 = SKIPPED</li></ul><h4 data-v-070f2274><b data-v-070f2274>error</b></h4><p data-v-070f2274>The error message if any error occurred running the <code data-v-070f2274>Job</code>.</p><h4 data-v-070f2274><b data-v-070f2274>runtimeVarsx</b></h4><p data-v-070f2274>Runtime variables for a <code data-v-070f2274>Job</code> are set when the <code data-v-070f2274>Job</code> is created manually (not by a schedule). If the <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JobDef</code> and any <code data-v-070f2274>Job</code> runtime variables have the same name as the <code data-v-070f2274>JobDef</code> runtime variables, the <code data-v-070f2274>Job</code> values will override the <code data-v-070f2274>JobDef</code> values.</p><p data-v-070f2274><code data-v-070f2274>{&quot;key1&quot;: &quot;value1&quot;, &quot;key2&quot;: &quot;value2&quot;}</code></p><p data-v-070f2274>If <code data-v-070f2274>true</code> the <code data-v-070f2274>Job</code> will run as a service.</p><h4 data-v-070f2274><b data-v-070f2274>runAsService</b></h4><p data-v-070f2274>runAsService Jobs can contain only one <code data-v-070f2274>Task</code>. The script in the last step of the <code data-v-070f2274>Task</code> must be written in such a way as to prevent the script from exiting, for example with a <code data-v-070f2274>while loop</code>. If the script exits (either naturally or by a crash) the <code data-v-070f2274>Job</code> will not be restarted.</p><p data-v-070f2274>If the Agent on which the <code data-v-070f2274>Job</code> is running fails to send a heartbeat for 30 minutes, the <code data-v-070f2274>Job</code> will be republished to another qualified Agent. If the original Agent subsequently connects and the <code data-v-070f2274>Job</code> is still running on that Agent, it will be <code data-v-070f2274>CANCELLED</code> on that Agent.</p><p data-v-070f2274>A runAsService <code data-v-070f2274>Job</code> will stop if it is in a <code data-v-070f2274>SUCCEEDED</code>, <code data-v-070f2274>CANCELLED</code> or <code data-v-070f2274>FAILED</code> state.</p><h3 data-v-070f2274>Actions</h3><h4 data-v-070f2274><b data-v-070f2274>interrupt</b></h4><p data-v-070f2274>Interrupt a <code data-v-070f2274>job</code>. <code data-v-070f2274>Job</code> status must be <code data-v-070f2274>RUNNING</code>.</p><p data-v-070f2274><code data-v-070f2274>jobaction/interrupt/:jobId</code></p><h4 data-v-070f2274><b data-v-070f2274>restart</b></h4><p data-v-070f2274>Restart a <code data-v-070f2274>job</code>. <code data-v-070f2274>Job</code> status must be <code data-v-070f2274>INTERRUPTED</code> or <code data-v-070f2274>FAILED</code>.</p><p data-v-070f2274><code data-v-070f2274>jobaction/restart/:jobId</code></p><h4 data-v-070f2274><b data-v-070f2274>cancel</b></h4><p data-v-070f2274>Cancel a job. Job status must be <code data-v-070f2274>NOT_STARTED</code>, <code data-v-070f2274>RUNNING</code>, <code data-v-070f2274>INTERRUPTING</code> or <code data-v-070f2274>INTERRUPTED</code>.</p><p data-v-070f2274><code data-v-070f2274>jobaction/cancel/:jobId</code></p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create and run an ad hoc job</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/job&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; --data &#39;{ &quot;job&quot;: { &quot;name&quot;: &quot;Job Name&quot;, &quot;dateCreated&quot;: &quot;[date]&quot;, &quot;tasks&quot;: [ { &quot;_teamId&quot;: &quot;[teamId]&quot;, &quot;name&quot;: &quot;Task1&quot;, &quot;requiredTags&quot;: , &quot;requiredTags&quot;: {&quot;tag&quot;: &quot;value&quot;}, &quot;dependsOn&quot;: [], &quot;steps&quot;: [ { &quot;name&quot;: &quot;Step1&quot;, &quot;script&quot;: { &quot;scriptType&quot;: &quot;SH&quot;, &quot;code&quot;: &quot;bHMgLWx0cgo=&quot; }, &quot;order&quot;: 0, &quot;arguments&quot;: &quot;&quot;, &quot;variables&quot;: &quot;&quot; } ] } ] } }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;dateCreated&quot;:&quot;[date]&quot;,\n        &quot;runAsService&quot;:false,\n        &quot;name&quot;:&quot;Job Name&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;dateStarted&quot;:&quot;[date]&quot;,\n        &quot;status&quot;:10,\n        &quot;runtimeVars&quot;:{},\n        &quot;id&quot;:&quot;[Job id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Job&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Create and run a job based on a JobDef template</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/job&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]’ --header &#39;_jobDefId: [jobDefId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;dateCreated&quot;:&quot;[date]&quot;,\n        &quot;runAsService&quot;:false,\n        &quot;_teamId&quot;:&quot;[date]&quot;,\n        &quot;_jobDefId&quot;:&quot;[date]&quot;,\n        &quot;runId&quot;:14,\n        &quot;name&quot;:&quot;[name]&quot;,\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;status&quot;:0,\n        &quot;runtimeVars&quot;:{},\n        &quot;id&quot;:&quot;[jobId]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Job&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get a job using the job id</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>&#39;https://saasglue.com/api/v0/job/[jobId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;dateCreated&quot;:&quot;[date]&quot;,\n        &quot;runAsService&quot;:false,\n        &quot;name&quot;:&quot;Job Name&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;dateStarted&quot;:&quot;[date]&quot;,\n        &quot;status&quot;:10,\n        &quot;runtimeVars&quot;:{},\n        &quot;id&quot;:&quot;[Job id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Job&quot;\n    }\n}\n                    </pre><h2 id=\"jobdef\" class=\"subtitle pb-2\" data-v-070f2274>JobDef</h2><p data-v-070f2274>A <code data-v-070f2274>JobDef</code> object is a template used to create and run a job. Typically a job template is created with the Job Designer and stored in a SaaSGlue account.</p><p data-v-070f2274>Jobs can also be created by submitting a <code data-v-070f2274>JobDef</code> object in JSON format to the API. In this way, ad hoc jobs can be created and executed dynamically.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>status</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>lastRunId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>maxInstances</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>misfireGraceTime</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>coalesce</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>createdBy</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateCreated</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runtimeVars</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>pauseOnFailedJob</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>JobDef</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>JobDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>This attribute stores a user created name for the <code data-v-070f2274>JobDef</code> object. The name should be descriptive but does not have to be unique.</p><h4 data-v-070f2274><b data-v-070f2274>status</b></h4><p data-v-070f2274>This attribute records the current status of the <code data-v-070f2274>JobDef</code> object. Here are the possible statuses.</p><ul data-v-070f2274><li data-v-070f2274>10 = <code data-v-070f2274>RUNNING</code> - indicates that new Jobs can be created and run using this <code data-v-070f2274>JobDef</code>.</li><li data-v-070f2274>15 = <code data-v-070f2274>PAUSED</code> - indicates that new Jobs from this <code data-v-070f2274>JobDef</code> can be created but will not run until the <code data-v-070f2274>JobDef</code> status is <code data-v-070f2274>RUNNING</code>.</li></ul><h4 data-v-070f2274><b data-v-070f2274>lastRunId</b></h4><p data-v-070f2274>Set to <code data-v-070f2274>0</code> for new <code data-v-070f2274>JobDef</code> objects and is incremented for each <code data-v-070f2274>Job</code> created from a given <code data-v-070f2274>JobDef</code>.</p><h4 data-v-070f2274><b data-v-070f2274>maxInstances</b></h4><p data-v-070f2274>The maximum number of parallel running Jobs created from the <code data-v-070f2274>JobDef</code>. For instance, if set to <code data-v-070f2274>1</code>, only one <code data-v-070f2274>Job</code> will run at a time. Any additional Jobs created will be queued to run when the current <code data-v-070f2274>Job</code> is finished.</p><h4 data-v-070f2274><b data-v-070f2274>misfireGraceTime</b></h4><p data-v-070f2274>The amount of time a <code data-v-070f2274>Job</code> can be queued past the scheduled start time before it is cancelled. This attribute can only be used with scheduled <code data-v-070f2274>JobDefs</code>. It is useful for situations when time is a critical factor in running a <code data-v-070f2274>Job</code>.</p><h4 data-v-070f2274><b data-v-070f2274>coalesce</b></h4><p data-v-070f2274>The coalesce attribute helps job designers determine how to handle situations where a <code data-v-070f2274>JobDef</code> object has multiple queued Jobs. This could occur if multiple Jobs are created but <code data-v-070f2274>maxInstances</code> is set to <code data-v-070f2274>1</code> or if Jobs are created when a <code data-v-070f2274>JobDef</code> is <code data-v-070f2274>PAUSED</code>. The next time SaaSGlue is able to run Jobs for the given <code data-v-070f2274>JobDef</code>, it may not be practical or desirable to run all the queued Jobs. If coalesce is set to <code data-v-070f2274>TRUE</code>, the first queued <code data-v-070f2274>Job</code> will run and the rest will be <code data-v-070f2274>SKIPPED</code> with the error message <code data-v-070f2274>Job skipped due to coalesce</code>. Otherwise all Jobs will run in parallel.</p><h4 data-v-070f2274><b data-v-070f2274>createdBy</b></h4><p data-v-070f2274>The ID of the User who created the <code data-v-070f2274>JobDef</code> object.</p><h4 data-v-070f2274><b data-v-070f2274>dateCreated</b></h4><p data-v-070f2274>The date/time the JobDef object was created.</p><h4 data-v-070f2274><b data-v-070f2274>runtimeVars</b></h4><p data-v-070f2274>A list of key/value pairs that can be dynamically injected into Job Scripts, <code data-v-070f2274>Script</code> arguments or environment variables at runtime.</p><p data-v-070f2274><code data-v-070f2274>{&quot;key1&quot;: &quot;value1&quot;, &quot;key2&quot;: &quot;value2&quot;}</code></p><h4 data-v-070f2274><b data-v-070f2274>pauseOnFailedJob</b></h4><p data-v-070f2274>This attribute can be used for example for <code data-v-070f2274>JobDef</code> objects with recurring Job executions that must complete in order. If a <code data-v-070f2274>Job</code> created from a <code data-v-070f2274>JobDef</code> is interrupted or fails and the <code data-v-070f2274>JobDef</code> <code data-v-070f2274>pauseOnFailedJob</code> attribute is <code data-v-070f2274>TRUE</code>, the <code data-v-070f2274>JobDef</code> Status will be updated to <code data-v-070f2274>PAUSED</code>. This will prevent all future <code data-v-070f2274>Job</code> executions until the <code data-v-070f2274>Job</code> is completed with status <code data-v-070f2274>SUCCEEDED</code>, <code data-v-070f2274>CANCELLED</code> or <code data-v-070f2274>SKIPPED</code> and there are no other <code data-v-070f2274>INTERRUPTED</code> or <code data-v-070f2274>FAILED</code> Jobs for this <code data-v-070f2274>JobDef</code>. Users can also manually pause or resume JobDefs.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create a JobDef</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;http://saasglue.com/api/v0/jobdef&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data &#39;{ &quot;_teamId&quot; : &quot;[teamId]&quot;, &quot;name&quot; : &quot;Job Name&quot; }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;status&quot;:10,\n        &quot;lastRunId&quot;:0,\n        &quot;maxInstances&quot;:1,\n        &quot;dateCreated&quot;:&quot;[date]&quot;,\n        &quot;pauseOnFailedJob&quot;:false,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;name&quot;:&quot;Job Name&quot;,\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;id&quot;:&quot;[id of new JobDef]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;JobDef&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get a JobDef using the jobDefId</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;http://saasglue.com/api/v0/jobdef/[JobDef id]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;status&quot;:10,\n        &quot;lastRunId&quot;:0,\n        &quot;maxInstances&quot;:1,\n        &quot;dateCreated&quot;:&quot;[date]&quot;,\n        &quot;pauseOnFailedJob&quot;:false,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;name&quot;:&quot;Job Name&quot;,\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;id&quot;:&quot;[JobDef id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;JobDef&quot;\n    }\n}\n                    </pre><h2 id=\"schedule\" class=\"subtitle pb-2\" data-v-070f2274>Schedule</h2><p data-v-070f2274>A Schedule defines when a <code data-v-070f2274>JobDef</code> runs. A <code data-v-070f2274>JobDef</code> can have multiple Schedules but a Schedule can only be assigned to a single <code data-v-070f2274>JobDef</code>.</p><h3 data-v-070f2274>General Schedule Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th><th data-v-070f2274>DELETE</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>createdBy</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>lastUpdatedBy</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>lastScheduledRunDate</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>nextScheduledRunDate</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>isActive</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>TriggerType</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>misfire_grace_time</code></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>coalesce</code></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>max_instances</code></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>RunDate</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x1</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>cron2</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>interval3</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><ul data-v-070f2274><li data-v-070f2274>1 Required only for <code data-v-070f2274>date</code> type Schedules</li><li data-v-070f2274>2 Consists of key value pairs defined in the <code data-v-070f2274>Cron Schedule Reference</code> – required for <code data-v-070f2274>cron</code> type Schedules</li><li data-v-070f2274>3 Consists of key value pairs defined in the <code data-v-070f2274>Interval Schedule Reference</code> – required for <code data-v-070f2274>interval</code> type Schedules</li></ul><h3 data-v-070f2274>Cron Schedule Reference</h3><p data-v-070f2274>This reference is to be used in connection with the General Schedule Reference above.</p><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Year</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Month</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Day</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Week</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Day_Of_Week</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Hour</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Minute</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Second</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Start_Date</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>End_Date</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Jitter</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><div class=\"notification is-info is-light\" data-v-070f2274>At least one attribute is required for a <code data-v-070f2274>cron</code> type Schedule</div><h3 data-v-070f2274>Interval Schedule Reference</h3><p data-v-070f2274>This reference is to be used in connection with the General Schedule Reference above.</p><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Weeks</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Days</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Hours</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Minutes</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Seconds</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Start_Date</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>End_Date</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>Jitter</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><div class=\"notification is-info is-light\" data-v-070f2274>At least one attribute is required for an <code data-v-070f2274>interval</code> type Schedule</div><h3 data-v-070f2274>General Schedule Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new Schedule object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>TaskDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>createdBy</b></h4><p data-v-070f2274>The ID of the user that created the Schedule.</p><h4 data-v-070f2274><b data-v-070f2274>lastUpdatedBy</b></h4><p data-v-070f2274>The ID of the last user to modify the Schedule.</p><h4 data-v-070f2274><b data-v-070f2274>lastScheduledRunDate</b></h4><p data-v-070f2274>The next date/time when the Schedule will be triggered.</p><h4 data-v-070f2274><b data-v-070f2274>nextScheduledRunDate</b></h4><p data-v-070f2274>The last date/time when the Schedule was triggered.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The user created name for a Schedule.</p><h4 data-v-070f2274><b data-v-070f2274>isActive</b></h4><p data-v-070f2274>If the isActive attribute is set to <code data-v-070f2274>false</code> no events for this Schedule will be triggered. If it is <code data-v-070f2274>true</code> future events will be triggered according to the Schedule but past skipped events will not be triggered.</p><h4 data-v-070f2274><b data-v-070f2274>TriggerType</b></h4><p data-v-070f2274>The type of trigger that kicks off a schedule, including <code data-v-070f2274>date</code>, <code data-v-070f2274>cron</code> or <code data-v-070f2274>interval</code>.</p><h4 data-v-070f2274><b data-v-070f2274>RunDated</b></h4><p data-v-070f2274>This object stores the date and time when a Schedule will execute if it has a <code data-v-070f2274>date</code> type Trigger.</p><h3 data-v-070f2274>Cron Schedule Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>Year</b></h4><p data-v-070f2274>The year in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Month</b></h4><p data-v-070f2274>The month in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Day</b></h4><p data-v-070f2274>The day in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Week</b></h4><p data-v-070f2274>The week in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Day_Of_Week</b></h4><p data-v-070f2274>The day of the week in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Hour</b></h4><p data-v-070f2274>The hour in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Minute</b></h4><p data-v-070f2274>The minute in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Second</b></h4><p data-v-070f2274>The second in a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Start_Date</b></h4><p data-v-070f2274>The earliest date/time a Schedule can be triggered for a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>End_Date</b></h4><p data-v-070f2274>The latest date/time a Schedule can be triggered for a <code data-v-070f2274>cron</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Jitter</b></h4><p data-v-070f2274>Will randomly advance or delay the scheduled start time for an event by jitter seconds for a <code data-v-070f2274>cron</code> TriggerType.</p><h3 data-v-070f2274>Interval Schedule Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>Weeks</b></h4><p data-v-070f2274>The weeks in an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Days</b></h4><p data-v-070f2274>The days in an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Hours</b></h4><p data-v-070f2274>The hours in an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Minutes</b></h4><p data-v-070f2274>The minutes in an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Seconds</b></h4><p data-v-070f2274>The seconds in an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Start_Date</b></h4><p data-v-070f2274>The earliest date/time a Schedule can be triggered for an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>End_Date</b></h4><p data-v-070f2274>The latest date/time a Schedule can be triggered for an <code data-v-070f2274>interval</code> TriggerType.</p><h4 data-v-070f2274><b data-v-070f2274>Jitter</b></h4><p data-v-070f2274>Will randomly advance or delay the scheduled start time for an event by jitter seconds for an <code data-v-070f2274>interval</code> TriggerType.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create a Schedule</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/schedule&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;_teamId&quot;: &quot;[teamId]]&quot;, &quot;_jobDefId&quot;: &quot;{jobDefId}&quot;, &quot;name&quot;: &quot;Schedule_1&quot;, &quot;isActive&quot;: false, &quot;TriggerType&quot;: &quot;interval&quot;, &quot;interval&quot;: { &quot;Seconds&quot;: 5 } }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;isActive&quot;:false,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;_jobDefId&quot;:&quot;[jobDefId]&quot;,\n        &quot;name&quot;:&quot;[Schedule name]&quot;,\n        &quot;TriggerType&quot;:&quot;interval&quot;,\n        &quot;interval&quot;:{\n            &quot;Seconds&quot;:5\n        },\n        &quot;createdBy&quot;:&quot;[userId]&quot;,\n        &quot;lastUpdatedBy&quot;:&quot;[userId]&quot;,\n        &quot;cron&quot;:{},\n        &quot;id&quot;:&quot;[Schedule ID]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Schedule&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get Schedules for a JobDef</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/schedule/?filter=_jobDefId%3D%3D[jobDefId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;isActive&quot;:false,\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;_jobDefId&quot;:&quot;[jobDefId]&quot;,\n            &quot;name&quot;:&quot;Schedule_1&quot;,\n            &quot;TriggerType&quot;:&quot;interval&quot;,\n            &quot;interval&quot;:{\n                &quot;Seconds&quot;:5\n            },\n            &quot;createdBy&quot;:&quot;[userId]&quot;,\n            &quot;lastUpdatedBy&quot;:&quot;[userId]&quot;,\n            &quot;cron&quot;:{},\n            &quot;id&quot;:&quot;[Schedule ID]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;Schedule&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h2 id=\"script\" class=\"subtitle pb-2\" data-v-070f2274>Script</h2><p data-v-070f2274>The Script object stores the script code and information associated with the script file to make it usable in the saas glue environment.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>scriptType</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>code</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_originalAuthorUserId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_lastEditedUserId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>teamEditable</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>lastEditedDate</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>Script</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>Script</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>Script</code> name must be unique.</p><h4 data-v-070f2274><b data-v-070f2274>scriptType</b></h4><p data-v-070f2274>SaaSGlue can be used to execute any script type. This attribute is added to the <code data-v-070f2274>Script</code> object to make it easier to work with common scripting languages. For example, the SaaSGlue cloud interface <code data-v-070f2274>Script</code> editor provides cues based on the script language. To use a script language not defined in SaaSGlue use type <code data-v-070f2274>SH</code> if the <code data-v-070f2274>Script</code> will run on Linux, Linux based or Mac operating systems and type <code data-v-070f2274>CMD</code> for Windows and then define the <code data-v-070f2274>Command</code> attribute either in the <code data-v-070f2274>StepDef</code> or when executing the <code data-v-070f2274>Script</code> from the Console.</p><p data-v-070f2274>The following script types are defined in SaaSGlue.</p><ul data-v-070f2274><li data-v-070f2274>0 = PYTHON</li><li data-v-070f2274>1 = NODE</li><li data-v-070f2274>2 = SH</li><li data-v-070f2274>3 = CMD</li><li data-v-070f2274>4 = RUBY</li><li data-v-070f2274>5 = LUA</li><li data-v-070f2274>6 = PERL</li><li data-v-070f2274>7 = PHP</li><li data-v-070f2274>8 = POWERSHELL</li></ul><h4 data-v-070f2274><b data-v-070f2274>code</b></h4><p data-v-070f2274>The code of the script encoded with <code data-v-070f2274>Base64</code>.</p><h4 data-v-070f2274><b data-v-070f2274>_originalAuthorUserId</b></h4><p data-v-070f2274>The ID of the user who first created the script.</p><h4 data-v-070f2274><b data-v-070f2274>_lastEditedUserId</b></h4><p data-v-070f2274>The ID of the user who last edited a script</p><h4 data-v-070f2274><b data-v-070f2274>teamEditable</b></h4><p data-v-070f2274>If set to <code data-v-070f2274>TRUE</code> anyone in the organization can edit the script.</p><h4 data-v-070f2274><b data-v-070f2274>lastEditedDate</b></h4><p data-v-070f2274>The date and time the Script was last edited.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create a new Script object</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/script&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;_teamId&quot;: &quot;[teamId]&quot;, &quot;name&quot;: &quot;[Script name]&quot;, &quot;scriptType&quot;: &quot;[Script type]&quot;, &quot;code&quot;: &quot;[Base64 encoded code]&quot;, &quot;lastEditedDate&quot;: &quot;[Date]&quot; }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;teamUsable&quot;:true,\n        &quot;teamEditable&quot;:false,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;name&quot;:&quot;[Script name]&quot;,\n        &quot;scriptType&quot;:2,\n        &quot;code&quot;:&quot;[Base64 encoded code]&quot;,\n        &quot;_originalAuthorUserId&quot;:&quot;[userId]&quot;,\n        &quot;_lastEditedUserId&quot;:&quot;[userId]&quot;,\n        &quot;lastEditedDate&quot;:&quot;[Date]&quot;,\n        &quot;shadowCopyCode&quot;:&quot;[Base64 encoded code (copy)]&quot;,\n        &quot;id&quot;:&quot;[Script id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;Script&quot;\n    }\n}\n                    </pre><h2 id=\"step\" class=\"subtitle pb-2\" data-v-070f2274>Step</h2><p data-v-070f2274>A <code data-v-070f2274>Job</code> consists of one or more Tasks, and a <code data-v-070f2274>Task</code> consists of one or more Steps.</p><p data-v-070f2274>A <code data-v-070f2274>Step</code> is the runtime implementation of a <code data-v-070f2274>StepDef</code>. Steps are only created when a containing <code data-v-070f2274>Job</code> is created. If a <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JobDef</code>, a <code data-v-070f2274>Step</code> will be created in the <code data-v-070f2274>Job</code> for each <code data-v-070f2274>StepDef</code> in the <code data-v-070f2274>JobDef</code>. If a <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JSON</code> template, a <code data-v-070f2274>Step</code> will be created in the <code data-v-070f2274>Job</code> for each <code data-v-070f2274>StepDef</code> embedded in the <code data-v-070f2274>JSON</code>.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_taskId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>order</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>script</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>command</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>arguments</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>variables</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>Step</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Org</code> object where the <code data-v-070f2274>Step</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Job</code> containing the <code data-v-070f2274>Step</code>.</p><h4 data-v-070f2274><b data-v-070f2274>_taskId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Task</code> containing the <code data-v-070f2274>Step</code>.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>Step</code> name. Is inherited from the associated <code data-v-070f2274>StepDef</code> object or from a <code data-v-070f2274>StepDef</code> embedded in a <code data-v-070f2274>JSON</code> formatted <code data-v-070f2274>Job</code> template.</p><h4 data-v-070f2274><b data-v-070f2274>order</b></h4><p data-v-070f2274>The order in which the <code data-v-070f2274>Step</code> will run in relation to other Steps in the same <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>script</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Script</code> the <code data-v-070f2274>Step</code> will execute.</p><h4 data-v-070f2274><b data-v-070f2274>command</b></h4><p data-v-070f2274>This attribute can be used to specify the exact interpreter to use to execute the <code data-v-070f2274>Step</code> script. It can also be used to set arguments that should be passed to that interpreter. For example, to pass a class path to the scala interpreter. If not specified, the default interpreter for the script type will be used to execute the script.</p><p data-v-070f2274>For example, if you have a script of type <code data-v-070f2274>python</code> and the <code data-v-070f2274>command</code> attribute is not set, then the default python interpreter on the machine where the <code data-v-070f2274>Step</code> runs will be used to execute the script. If the command attribute is set to <code data-v-070f2274>/usr/bin/python2.7</code>, then the interpreter in that location on the <code data-v-070f2274>Agent</code> machine will be used to run the script. The interpreter specified by the command attribute must exist on the machine on which the <code data-v-070f2274>Step</code> runs.</p><h4 data-v-070f2274><b data-v-070f2274>arguments</b></h4><p data-v-070f2274>A space delimited list of arguments which will be passed to the script upon execution. Syntax follows normal command line arguments, e.g. a single argument with spaces should be enclosed in quotes.</p><h4 data-v-070f2274><b data-v-070f2274>variables</b></h4><p data-v-070f2274>Environment variables to set in the script runtime environment. Use this attribute to set environment variables required by the script which may not exist on the target machine(s) or which you want to override.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Get all Step objects in a Task</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/step?filter=_taskId%3D%3D[taskId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;command&quot;:&quot;&quot;,\n            &quot;arguments&quot;:&quot;&quot;,\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;_jobId&quot;:&quot;[jobId]&quot;,\n            &quot;_taskId&quot;:&quot;[taskId]&quot;,\n            &quot;name&quot;:&quot;step1&quot;,\n            &quot;order&quot;:0,\n            &quot;script&quot;:{\n                &quot;id&quot;:&quot;[scriptId&quot;,\n                &quot;name&quot;:&quot;echo hello&quot;,\n                &quot;scriptType&quot;:2,\n                &quot;code&quot;:&quot;ZWNobyAiSGVsbG8gV29ybGQi&quot;\n            },\n            &quot;id&quot;:&quot;[Step id]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;Step&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h2 id=\"stepdef\" class=\"subtitle pb-2\" data-v-070f2274>StepDef</h2><p data-v-070f2274>The <code data-v-070f2274>StepDef</code> object defines how a <code data-v-070f2274>Step</code> is to be Run within a <code data-v-070f2274>Task</code>.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_taskDefId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_scriptId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>order</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>command</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>arguments</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>variables</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>StepDef</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Org</code> object where the <code data-v-070f2274>StepDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_taskDefId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>TaskDef</code> object containing the <code data-v-070f2274>StepDef</code>.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>StepDef</code> name. Must be unique among StepDefs within the <code data-v-070f2274>TaskDef</code> to which it belongs.</p><h4 data-v-070f2274><b data-v-070f2274>_scriptId</b></h4><p data-v-070f2274>The ID of the script which Steps created from this <code data-v-070f2274>StepDef</code> will execute.</p><h4 data-v-070f2274><b data-v-070f2274>order</b></h4><p data-v-070f2274>The order in which Steps created from the <code data-v-070f2274>StepDef</code> will run.</p><h4 data-v-070f2274><b data-v-070f2274>command</b></h4><p data-v-070f2274>This attribute can be used to specify the exact interpreter to use to execute the script for Steps created from this <code data-v-070f2274>StepDef</code>. It can also be used to set arguments that should be passed to that interpreter. For example, to pass a class path to the scala interpreter. If not specified, the default interpreter for the script type will be used to execute the script.</p><p data-v-070f2274>For example, if you have a script of type <code data-v-070f2274>python</code> and the <code data-v-070f2274>command</code> attribute is not set, then the default <code data-v-070f2274>python</code> interpreter on the machine where the <code data-v-070f2274>Step</code> runs will be used to execute the script. If the command attribute is set to <code data-v-070f2274>/usr/bin/python2.7</code>, then the interpreter in that location on the <code data-v-070f2274>Agent</code> machine will be used to run the script. The interpreter specified by the command attribute must exist on the machine on which the <code data-v-070f2274>Step</code> runs.</p><h4 data-v-070f2274><b data-v-070f2274>arguments</b></h4><p data-v-070f2274>A space delimited list of command line arguments which will be passed to the script upon execution. Syntax follows normal command line arguments, e.g. a single argument with spaces should be enclosed in quotes.</p><h4 data-v-070f2274><b data-v-070f2274>variables</b></h4><p data-v-070f2274>Environment variables to set in the script runtime environment. Use this attribute to set environment variables required by the script which may not exist on the target machine(s) or which you want to override.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create a StepDef object</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/stepdef&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;_teamId&quot;: &quot;[teamId]&quot;, &quot;_taskDefId&quot;: &quot;[taskDefId]&quot;, &quot;name&quot;: &quot;[Step name]&quot;, &quot;_scriptId&quot;: &quot;[scriptId]&quot;, &quot;order&quot;: &quot;0&quot;, &quot;arguments&quot;: &quot;&quot;, &quot;variables&quot;: &quot;&quot; }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;_scriptId&quot;:&quot;[scriptId]&quot;,\n        &quot;command&quot;:&quot;&quot;,\n        &quot;arguments&quot;:&quot;&quot;,\n        &quot;variables&quot;:{},\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;_taskDefId&quot;:&quot;[taskDefId]&quot;,\n        &quot;name&quot;:&quot;step1&quot;,\n        &quot;order&quot;:0,\n        &quot;id&quot;:&quot;[StepDef id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;StepDef&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get a single StepDef object</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/stepdef/[stepDefId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;_scriptId&quot;:&quot;[ScriptId]&quot;,\n        &quot;command&quot;:&quot;&quot;,\n        &quot;arguments&quot;:&quot;&quot;,\n        &quot;variables&quot;:{},\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;_taskDefId&quot;:&quot;[TaskDefId]&quot;,\n        &quot;name&quot;:&quot;step1&quot;,\n        &quot;order&quot;:0,\n        &quot;id&quot;:&quot;[StepDef id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;StepDef&quot;\n    }\n}\n                    </pre><h2 id=\"stepoutcome\" class=\"subtitle pb-2\" data-v-070f2274>StepOutcome</h2><p data-v-070f2274>The <code data-v-070f2274>StepOutcome</code> object stores the output of a <code data-v-070f2274>Step</code> execution. A <code data-v-070f2274>Step</code> can be executed at most once for each execution of the <code data-v-070f2274>Task</code> to which it belongs. Steps in a <code data-v-070f2274>Task</code> are executed in order until they all complete successfully or one fails. A <code data-v-070f2274>Step</code> is considered to have failed if it completes with a non-zero exit code.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_stepId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_taskOutcomeId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_invoiceId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>machineId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>ipAddress</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runCode</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runtimeVars</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>stdout</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>stderr</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>exitCode</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>signal</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>status</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateStarted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateCompleted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>tail</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>StepOutcome</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>StepOutcome</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Job</code> containing the Step.</p><h4 data-v-070f2274><b data-v-070f2274>_stepId</b></h4><p data-v-070f2274>The Step ID from which the <code data-v-070f2274>StepOutcome</code> is derived.</p><h4 data-v-070f2274><b data-v-070f2274>_taskOutcomeId</b></h4><p data-v-070f2274>The <code data-v-070f2274>TaskOutcome</code> object associated with the <code data-v-070f2274>StepOutcome</code>.</p><h4 data-v-070f2274><b data-v-070f2274>_invoiceId</b></h4><p data-v-070f2274>The ID of the Invoice where the <code data-v-070f2274>StepOutcome</code> was billed.</p><h4 data-v-070f2274><b data-v-070f2274>machineId</b></h4><p data-v-070f2274>The Machine ID where the <code data-v-070f2274>Step</code> executed.</p><h4 data-v-070f2274><b data-v-070f2274>ipAddress</b></h4><p data-v-070f2274>The local IP address of the Machine where the <code data-v-070f2274>Step</code> executed.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The name of the step. This is inherited from the <code data-v-070f2274>Step</code> Object from which the <code data-v-070f2274>StepOutcome</code> is derived.</p><h4 data-v-070f2274><b data-v-070f2274>runCode</b></h4><p data-v-070f2274>The script code that was executed in the <code data-v-070f2274>Step</code>. The code is encoded with Base64.</p><h4 data-v-070f2274><b data-v-070f2274>runtimeVars</b></h4><p data-v-070f2274>A list of key-value pair “runtime variables” generated by this <code data-v-070f2274>Step</code>. These variables can be consumed/modified by subsequent Steps in the same <code data-v-070f2274>Task</code> and by subsequent Tasks in the same <code data-v-070f2274>Job</code> workflow.</p><h4 data-v-070f2274><b data-v-070f2274>stdout</b></h4><p data-v-070f2274>The standard output generated by the <code data-v-070f2274>Script</code> execution.</p><h4 data-v-070f2274><b data-v-070f2274>stderr</b></h4><p data-v-070f2274>Any errors generated by the <code data-v-070f2274>Script</code> execution.</p><h4 data-v-070f2274><b data-v-070f2274>exitCode</b></h4><p data-v-070f2274>The <code data-v-070f2274>Script</code> exit code.</p><h4 data-v-070f2274><b data-v-070f2274>signal</b></h4><p data-v-070f2274>The signal used to stop the <code data-v-070f2274>Script</code> execution If it was stopped externally. For example <code data-v-070f2274>SIGTERM</code> or <code data-v-070f2274>SIGKILL</code>.</p><h4 data-v-070f2274><b data-v-070f2274>status</b></h4><p data-v-070f2274>The current status of the <code data-v-070f2274>Script</code> execution. Here are the possible values.</p><ul data-v-070f2274><li data-v-070f2274>0 = NOT_STARTED</li><li data-v-070f2274>10 = RUNNING</li><li data-v-070f2274>15 = INTERRUPTED</li><li data-v-070f2274>20 = SUCCEEDED</li><li data-v-070f2274>21 = CANCELLED</li><li data-v-070f2274>22 = FAILED</li></ul><h4 data-v-070f2274><b data-v-070f2274>dateStarted</b></h4><p data-v-070f2274>The date and time the <code data-v-070f2274>Script</code> was started.</p><h4 data-v-070f2274><b data-v-070f2274>dateCompleted</b></h4><p data-v-070f2274>The date and time the <code data-v-070f2274>Script</code> completed.</p><h4 data-v-070f2274><b data-v-070f2274>tail</b></h4><p data-v-070f2274>The last five lines of the standard output. It will be updated as the <code data-v-070f2274>Script</code> creates additional stdout.</p><p data-v-070f2274>If the user configures the <code data-v-070f2274>Script</code> to pipe stdout to a file the tail attribute will not be updated during <code data-v-070f2274>Script</code> execution. However, any runtime variable values will be extracted from the file to which stdout is piped.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Get all StepOutcome objects for a TaskOutcome</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/stepOutcome?filter=_taskOutcomeId%3D%3D[taskOutcomeId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;tail&quot;:[\n                &quot;start&quot;,\n                &quot;@kpo{\\&quot;outVal\\&quot;: \\&quot;val\\&quot;}&quot;,\n                &quot;done&quot;\n            ],\n            &quot;lastUpdateId&quot;:2,\n            &quot;archived&quot;:false,\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;_jobId&quot;:&quot;[jobId]&quot;,\n            &quot;_stepId&quot;:&quot;[stepId]&quot;,\n            &quot;_taskOutcomeId&quot;:&quot;[taskOutcomeId]&quot;,\n            &quot;name&quot;:&quot;[Step Name]&quot;,\n            &quot;machineId&quot;:&quot;[Agent MachineId]&quot;,\n            &quot;ipAddress&quot;:&quot;[Agent IP Address]&quot;,\n            &quot;runCode&quot;:&quot;[Base64 encoded script code]&quot;,\n            &quot;status&quot;:20,\n            &quot;dateStarted&quot;:&quot;[Date]&quot;,\n            &quot;dateCompleted&quot;:&quot;[Date]&quot;,\n            &quot;exitCode&quot;:0,\n            &quot;runtimeVars&quot;:{\n                &quot;outVal&quot;:&quot;val&quot;\n            },\n            &quot;signal&quot;:null,\n            &quot;stderr&quot;:&quot;&quot;,\n            &quot;stdout&quot;:&quot;start\\n@kpo{\\&quot;outVal\\&quot;: \\&quot;val\\&quot;}\\ndone\\n&quot;,\n            &quot;id&quot;:&quot;[StepOutcome id]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;StepOutcome&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h2 id=\"task\" class=\"subtitle pb-2\" data-v-070f2274>Task</h2><p data-v-070f2274>A <code data-v-070f2274>Job</code> consists of one or more Tasks.</p><p data-v-070f2274>A <code data-v-070f2274>Task</code> is the runtime implementation of a <code data-v-070f2274>TaskDef</code>. Tasks are only created when a containing <code data-v-070f2274>Job</code> is created. If a <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JobDef</code>, a <code data-v-070f2274>Task</code> will be created in the <code data-v-070f2274>Job</code> for each <code data-v-070f2274>TaskDef</code> in the <code data-v-070f2274>JobDef</code>. If a <code data-v-070f2274>Job</code> is created from a <code data-v-070f2274>JSON</code> template, a <code data-v-070f2274>Task</code> will be created in the <code data-v-070f2274>Job</code> for each <code data-v-070f2274>TaskDef</code> embedded in the <code data-v-070f2274>JSON</code>.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>target</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>targetAgentId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>requiredTags</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>fromRoutes</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>toRoutes</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>sourceTaskRoute</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>artifacts</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>status</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>error</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>failureCode</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runtimeVars</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>route</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>TaskDef</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>TaskDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Job</code> containing the <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>target</b></h4><p data-v-070f2274>Defines which Agent(s) are qualified to run the <code data-v-070f2274>Task</code>.</p><ul data-v-070f2274><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents within the Org.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent in the Org.</li><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_SPECIFIC_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to a single specific Agent. The Agent ID must be listed in the <code data-v-070f2274>targetAgentId</code> attribute of the <code data-v-070f2274>TaskDef</code> object.</li></ul><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>Task</code> name. Is inherited from the associated <code data-v-070f2274>TaskDef</code> object or from a <code data-v-070f2274>TaskDef</code> embedded in a <code data-v-070f2274>JSON</code> formatted <code data-v-070f2274>Job</code> template.</p><h4 data-v-070f2274><b data-v-070f2274>targetAgentId</b></h4><p data-v-070f2274>The ID of the Agent where the <code data-v-070f2274>Task</code> will run. This attribute is only relevant if the <code data-v-070f2274>target</code> attribute is set to <code data-v-070f2274>SINGLE_AGENT</code>.</p><h4 data-v-070f2274><b data-v-070f2274>requiredTags</b></h4><p data-v-070f2274>Executing Agents must have tags matching all tags in this attribute if the <code data-v-070f2274>target</code> attribute is set to <code data-v-070f2274>SINGLE_AGENT_WITH_TAGS</code> or <code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code>.</p><p data-v-070f2274>For example if <code data-v-070f2274>requiredTags</code> is <code data-v-070f2274>&quot;[{&#39;os&#39;: &#39;linux&#39;}, {&#39;java_installed&#39;: &#39;true&#39;}]&quot;</code> then only Agents with matching tags will be considered to run the <code data-v-070f2274>Task</code>. The Agents may have additional tags not in the list of <code data-v-070f2274>requiredTags</code> and still be qualified.</p><h4 data-v-070f2274><b data-v-070f2274>fromRoutes</b></h4><p data-v-070f2274>The names and optional route codes of preceding Tasks which must <code data-v-070f2274>ALL</code> be completed before the <code data-v-070f2274>Task</code> will be executed. For example <code data-v-070f2274>&quot;[ [&#39;Task1&#39;], [&#39;Task2&#39;, &#39;ok&#39;] ]&quot;</code> indicates that <code data-v-070f2274>Task1</code> must be completed with any route (other than <code data-v-070f2274>fail</code>) and <code data-v-070f2274>Task2</code> must be complete with route <code data-v-070f2274>ok</code> for the <code data-v-070f2274>Task</code> to execute.</p><p data-v-070f2274>Route codes are generated by Scripts within a <code data-v-070f2274>Task</code> by printing a formatted message to standard output. For example <code data-v-070f2274>&quot;print &#39;@sgg{&quot;route&quot;, &quot;ok&quot;}&#39;&quot;</code> in a python script will generate the route <code data-v-070f2274>ok</code> for routing to downstream Tasks.</p><p data-v-070f2274>If a <code data-v-070f2274>Task</code> cannot be routed to based on prior route code dependencies it&#39;s status will be set to <code data-v-070f2274>SKIPPED</code>.</p><h4 data-v-070f2274><b data-v-070f2274>toRoutes</b></h4><p data-v-070f2274>The names of Tasks to launch when the <code data-v-070f2274>Task</code> completes. This is different from the <code data-v-070f2274>fromRoutes</code> attribute in that every time the current <code data-v-070f2274>Task</code> completes every <code data-v-070f2274>Task</code> in <code data-v-070f2274>toRoutes</code> with a matching route will be launched. For example a <code data-v-070f2274>toRoute</code> value of <code data-v-070f2274>&quot;[ [&#39;Task1&#39;], [&#39;Task2&#39;, &#39;ok&#39;] ]&quot;</code> would result in <code data-v-070f2274>Task1</code> and <code data-v-070f2274>Task2</code> getting launched if the current task completes with route <code data-v-070f2274>ok</code>.</p><p data-v-070f2274>If the current <code data-v-070f2274>Task</code> targets multiple agents (<code data-v-070f2274>target = ALL_AGENTS</code> or <code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code>) and the current <code data-v-070f2274>Task</code> completes multiple times, the matching <code data-v-070f2274>toRoute</code> Tasks will be launched for each successful completion of the current <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>sourceTaskRoute</b></h4><p data-v-070f2274>For Tasks that were routed to with a <code data-v-070f2274>to</code> route, the <code data-v-070f2274>Task</code> id and route that preceded this <code data-v-070f2274>Task</code>, eg <code data-v-070f2274>{ sourceTaskOutcomeId: [id], sourceRoute: [route] }</code></p><h4 data-v-070f2274><b data-v-070f2274>artifacts</b></h4><p data-v-070f2274>A list of Artifact IDs to be used to execute the <code data-v-070f2274>Task</code>. Artifacts can include configuration files, compiled java, .net or native code, datafiles, or any other file required to execute the Scripts in a <code data-v-070f2274>Task</code>. Artifacts are saved on the Org level and can be uploaded via the API or the web console.</p><h4 data-v-070f2274><b data-v-070f2274>status</b></h4><p data-v-070f2274>The current Task status.</p><ul data-v-070f2274><li data-v-070f2274>NOT_STARTED = 0</li><li data-v-070f2274>PUBLISHED = 5</li><li data-v-070f2274>RUNNING = 10</li><li data-v-070f2274>INTERRUPTING = 14</li><li data-v-070f2274>INTERRUPTED = 15</li><li data-v-070f2274>CANCELING = 17</li><li data-v-070f2274>SUCCEEDED = 20</li><li data-v-070f2274>CANCELLED = 21</li><li data-v-070f2274>FAILED = 22</li><li data-v-070f2274>SKIPPED = 23</li></ul><h4 data-v-070f2274><b data-v-070f2274>error</b></h4><p data-v-070f2274>Text of any error that occurred running the <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>failureCode</b></h4><p data-v-070f2274>The failure code for a Task with FAILED status.</p><ul data-v-070f2274><li data-v-070f2274>AGENT_CRASHED_OR_LOST_CONNECTIVITY = 0</li><li data-v-070f2274>NO_AGENT_AVAILABLE = 1</li><li data-v-070f2274>AGENT_EXEC_ERROR = 2</li><li data-v-070f2274>QUEUED_TASK_EXPIRED = 3</li><li data-v-070f2274>TARGET_AGENT_NOT_SPECIFIED = 4</li><li data-v-070f2274>MISSING_TARGET_TAGS = 5</li></ul><h4 data-v-070f2274><b data-v-070f2274>runtimeVars</b></h4><p data-v-070f2274>A list of key-value pair <code data-v-070f2274>runtime variables</code> which will be used to execute scripts in the Steps for this <code data-v-070f2274>Task</code>. These variables can be dynamically injected at runtime in script arguments, environment variables or in scripts themselves.</p><p data-v-070f2274>Strings formatted as <code data-v-070f2274>sgg(&quot;[variable name]&quot;)</code> in script arguments, environment variables or scripts will be replaced with the most recent value of the runtime variable named <code data-v-070f2274>[variable name]</code>. If a runtime variable with that name does not exist, it will be replaced with <code data-v-070f2274>null</code>.</p><p data-v-070f2274>Runtime variable values can be set at design time on the Org or <code data-v-070f2274>JobDef</code> level. They can be set at runtime when Jobs are created and they can be dynamically generated/modified during script execution by printing correctly formatted strings to stdout. The format is <code data-v-070f2274>@sgo{&quot;[key]&quot;: &quot;[value]&quot;}</code>, e.g. the following line in a <code data-v-070f2274>python</code> script would set the value of &quot;my_key&quot; to <code data-v-070f2274>&quot;my_value&quot;: print(&#39;@sgo{&quot;my_key&quot;: &quot;my_value&quot;}&#39;)</code>.</p><h4 data-v-070f2274><b data-v-070f2274>TTL</b></h4><p data-v-070f2274>The time (in milliseconds) to wait for a task to be picked up by an Agent for processing before routing the task with status <code data-v-070f2274>fail</code>.</p><h3 data-v-070f2274>Code Sample</h3><h4 data-v-070f2274>Get all Task objects in a Job</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/task?filter=_jobId%3D%3D[teamId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;fromRoutes&quot;:[],\n            &quot;toRoutes&quot;:[],\n            &quot;artifacts&quot;:[],\n            &quot;status&quot;:22,\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;_jobId&quot;:&quot;[jobId]&quot;,\n            &quot;name&quot;:&quot;[Task name]&quot;,\n            &quot;targetAgentId&quot;:null,\n            &quot;requiredTags&quot;:{\n                &quot;numchucks&quot;:&quot;true&quot;\n            },\n            &quot;target&quot;:4,\n            &quot;runtimeVars&quot;:{},\n            &quot;TTL&quot;:null,\n            &quot;failureCode&quot;:1,\n            &quot;id&quot;:&quot;[Task id]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;Task&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h2 id=\"taskdef\" class=\"subtitle pb-2\" data-v-070f2274>TaskDef</h2><p data-v-070f2274>The <code data-v-070f2274>TaskDef</code> object defines how a <code data-v-070f2274>Task</code> is to be run within a <code data-v-070f2274>Job</code>.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobDefId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>target</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>targetAgentId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>requiredTags</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>fromRoutes</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>toRoutes</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>artifacts</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>TTL</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>TaskDef</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>TaskDef</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobDefId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>JobDef</code> containing the <code data-v-070f2274>TaskDef</code>.</p><h4 data-v-070f2274><b data-v-070f2274>target</b></h4><p data-v-070f2274>Defines which Agent(s) are qualified to run Tasks created from this <code data-v-070f2274>TaskDef</code>.</p><ul data-v-070f2274><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents within the Org.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent in the Org.</li><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_SPECIFIC_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to a single specific Agent. The Agent ID must be listed in the <code data-v-070f2274>targetAgentId</code> attribute of the <code data-v-070f2274>TaskDef</code> object.</li></ul><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>TaskDef</code> name. Must be unique among TaskDefs within the <code data-v-070f2274>JobDef</code> to which it belongs.</p><h4 data-v-070f2274><b data-v-070f2274>targetAgentId</b></h4><p data-v-070f2274>The ID of the Agent where Tasks created from the <code data-v-070f2274>TaskDef</code> will run. This attribute is only relevant if the <code data-v-070f2274>target</code> attribute is set to <code data-v-070f2274>SINGLE_AGENT</code>.</p><h4 data-v-070f2274><b data-v-070f2274>requiredTags</b></h4><p data-v-070f2274>Executing Agents must have tags matching all tags in this attribute if the <code data-v-070f2274>target</code> attribute is set to <code data-v-070f2274>SINGLE_AGENT_WITH_TAGS</code> or <code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code>.</p><p data-v-070f2274>For example if <code data-v-070f2274>requiredTags</code> is <code data-v-070f2274>[{&#39;os&#39;: &#39;linux&#39;}, {&#39;java_installed&#39;: &#39;true&#39;}]</code> then only Agents with matching tags will be considered to run Tasks created from the <code data-v-070f2274>TaskDef</code>. The Agents may have additional tags not in the list of <code data-v-070f2274>requiredTags</code> and still be qualified.</p><h4 data-v-070f2274><b data-v-070f2274>fromRoutes</b></h4><p data-v-070f2274>The names and optional route codes of preceding Tasks which must ALL be completed before a <code data-v-070f2274>Task</code> created from this <code data-v-070f2274>TaskDef</code> will be executed. For example <code data-v-070f2274>[ [&#39;Task1&#39;], [&#39;Task2&#39;, &#39;ok&#39;] ]</code> indicates that <code data-v-070f2274>Task1</code> must be completed with any route (other than <code data-v-070f2274>fail</code>) and <code data-v-070f2274>Task2</code> must be complete with route <code data-v-070f2274>ok</code> for the <code data-v-070f2274>Task</code> to execute.</p><p data-v-070f2274>Route codes are generated by Scripts within a <code data-v-070f2274>Task</code> by printing a formatted message to standard output. For example <code data-v-070f2274>print &#39;@sgg{&quot;route&quot;, &quot;ok&quot;}&#39;</code> in a python script will generate the route <code data-v-070f2274>ok</code> for routing to downstream Tasks.</p><p data-v-070f2274>If a <code data-v-070f2274>Task</code> cannot be routed to based on prior route code dependencies it&#39;s status will be set to <code data-v-070f2274>SKIPPED</code>.</p><h4 data-v-070f2274><b data-v-070f2274>toRoutes</b></h4><p data-v-070f2274>The names of Tasks to launch when the <code data-v-070f2274>Task</code> completes. This is different from the <code data-v-070f2274>fromRoutes</code> attribute in that every time the current <code data-v-070f2274>Task</code> completes every <code data-v-070f2274>Task</code> in <code data-v-070f2274>toRoutes</code> with a matching route will be launched. For example a <code data-v-070f2274>toRoute</code> value of <code data-v-070f2274>[ [&#39;Task1&#39;], [&#39;Task2&#39;, &#39;ok&#39;] ]</code> would result in <code data-v-070f2274>Task1</code> and <code data-v-070f2274>Task2</code> getting launched if the current task completes with route <code data-v-070f2274>ok</code>.</p><p data-v-070f2274>If the current <code data-v-070f2274>Task</code> targets multiple agents (<code data-v-070f2274>target = ALL_AGENTS</code> or <code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code>) and the current <code data-v-070f2274>Task</code> completes multiple times, the matching <code data-v-070f2274>toRoute</code> Tasks will be launched for each successful completion of the current <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>artifacts</b></h4><p data-v-070f2274>A list of Artifact IDs to be used to execute a <code data-v-070f2274>Task</code> created from the <code data-v-070f2274>TaskDef</code>. Artifacts can include configuration files, compiled java, .net or native code, datafiles, or any other file required to execute the Scripts in a <code data-v-070f2274>Task</code>. Artifacts are saved on the Org level and can be uploaded via the API or the web console.</p><h4 data-v-070f2274><b data-v-070f2274>TTL</b></h4><p data-v-070f2274>The time (in milliseconds) to wait for a task to be picked up by an Agent for processing before routing the task with status <code data-v-070f2274>fail</code>.</p><h3 data-v-070f2274>Code Sample</h3><h4 data-v-070f2274>Create a TaskDef object</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/taskdef&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;name&quot;: &quot;[Task name]&quot;, &quot;_teamId&quot;: &quot;[teamId]&quot;, &quot;_jobDefId&quot;: &quot;[jobDefId]]&quot;, &quot;requiredTags&quot;: [], &quot;runOnAllAgents&quot;: &quot;false&quot;, &quot;fromRoutes&quot;: [], &quot;target&quot;: 0 }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;fromRoutes&quot;:[],\n        &quot;toRoutes&quot;:[],\n        &quot;artifacts&quot;:[],\n        &quot;name&quot;:&quot;[Task name]&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;_jobDefId&quot;:&quot;[jobDefId]&quot;,\n        &quot;requiredTags&quot;:[],\n        &quot;target&quot;:0,\n        &quot;id&quot;:&quot;[TaskDef id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;TaskDef&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get a single TaskDef object</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;http://saasglue.com/api/v0/taskdef/[TaskDef id]]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;fromRoutes&quot;:[],\n        &quot;toRoutes&quot;:[],\n        &quot;artifacts&quot;:[],\n        &quot;name&quot;:&quot;[Task name]&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;_jobDefId&quot;:&quot;[jobDefId]&quot;,\n        &quot;requiredTags&quot;:[],\n        &quot;target&quot;:0,\n        &quot;id&quot;:&quot;[TaskDef id]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;TaskDef&quot;\n    }\n}\n                    </pre><h2 id=\"taskoutcome\" class=\"subtitle pb-2\" data-v-070f2274>TaskOutcome</h2><p data-v-070f2274>The <code data-v-070f2274>TaskOutcome</code> object stores the output of a <code data-v-070f2274>Task</code> execution. Since a single <code data-v-070f2274>Task</code> can be executed multiple times in a <code data-v-070f2274>Job</code>, a <code data-v-070f2274>Task</code> can have multiple TaskOutcomes.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>POST (optional)</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_jobId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_taskId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_agentId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>sourceTaskRoute</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>target</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>status</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>route</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>failureCode</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateStarted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>dateCompleted</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>ipAddress</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>machineId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>artifactsDownloadedSize</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>runtimeVars</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>TaskOutcome</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Org object where the <code data-v-070f2274>TaskOutcome</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>_jobId</b></h4><p data-v-070f2274>The ID of the <code data-v-070f2274>Job</code> containing the <code data-v-070f2274>TaskOutcome</code>.</p><h4 data-v-070f2274><b data-v-070f2274>_taskId</b></h4><p data-v-070f2274>The Task ID from which the <code data-v-070f2274>TaskOutcome</code> is derived.</p><h4 data-v-070f2274><b data-v-070f2274>_agentId</b></h4><p data-v-070f2274>The ID of the Agent that executed the <code data-v-070f2274>Task</code>.</p><h4 data-v-070f2274><b data-v-070f2274>sourceTaskRoute</b></h4><p data-v-070f2274>For Tasks that were routed to with a <code data-v-070f2274>to</code> route, the <code data-v-070f2274>Task</code> id and route that preceded this <code data-v-070f2274>Task</code>, eg <code data-v-070f2274>{ sourceTaskOutcomeId: [id], sourceRoute: [route] }</code></p><h4 data-v-070f2274><b data-v-070f2274>target</b></h4><p data-v-070f2274>Defines which Agent(s) are qualified to run the <code data-v-070f2274>Task</code>.</p><ul data-v-070f2274><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents within the Org.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent in the Org.</li><li data-v-070f2274><code data-v-070f2274>ALL_AGENTS_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to all Agents with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_AGENT_WITH_TAGS</code> - The <code data-v-070f2274>Task</code> will be routed to any single Agent with all tags in the <code data-v-070f2274>requiredTags</code> field for this <code data-v-070f2274>Task</code>.</li><li data-v-070f2274><code data-v-070f2274>SINGLE_SPECIFIC_AGENT</code> - The <code data-v-070f2274>Task</code> will be routed to a single specific Agent. The Agent ID must be listed in the <code data-v-070f2274>targetAgentId</code> attribute of the <code data-v-070f2274>TaskDef</code> object.</li></ul><h4 data-v-070f2274><b data-v-070f2274>status</b></h4><p data-v-070f2274>This attribute stores the current status of the <code data-v-070f2274>Task</code> execution.</p><ul data-v-070f2274><li data-v-070f2274>NOT_STARTED = 0</li><li data-v-070f2274>PUBLISHED = 5</li><li data-v-070f2274>RUNNING = 10</li><li data-v-070f2274>INTERRUPTING = 14</li><li data-v-070f2274>INTERRUPTED = 15</li><li data-v-070f2274>CANCELING = 17</li><li data-v-070f2274>SUCCEEDED = 20</li><li data-v-070f2274>CANCELLED = 21</li><li data-v-070f2274>FAILED = 22</li><li data-v-070f2274>SKIPPED = 23</li></ul><h4 data-v-070f2274><b data-v-070f2274>route</b></h4><p data-v-070f2274>The route used to determine the conditional path for Tasks subsequent to the <code data-v-070f2274>TaskOutcome</code>.</p><p data-v-070f2274>The default route code is blank if a <code data-v-070f2274>Task</code> completes successfully, <code data-v-070f2274>fail</code> if a <code data-v-070f2274>Task</code> does not complete successfully and <code data-v-070f2274>interrupt</code> if a <code data-v-070f2274>Task</code> is interrupted intentionally. A <code data-v-070f2274>Task</code> can be interrupted by sending a <code data-v-070f2274>SIGTERM/SIGKILL</code> signal to the Agent process (equivalent to <code data-v-070f2274>CTRL+C</code>), which interrupts all running processes, or via the API or Web application.</p><p data-v-070f2274>Routes can also be set during script execution by printing a correctly formatted string to stdout. The format is <code data-v-070f2274>@sgo{&quot;route&quot;: &quot;[route]&quot;}</code>, e.g. the following line in a python script would set the route to <code data-v-070f2274>&quot;ok&quot;: print(&#39;@sgo{&quot;route&quot;: &quot;ok&quot;}&#39;)</code>.</p><h4 data-v-070f2274><b data-v-070f2274>failureCode</b></h4><p data-v-070f2274>The failure code for a <code data-v-070f2274>TaskOutcome</code> with <code data-v-070f2274>FAILED</code> status.</p><ul data-v-070f2274><li data-v-070f2274>AGENT_CRASHED_OR_LOST_CONNECTIVITY = 0</li><li data-v-070f2274>NO_AGENT_AVAILABLE = 1</li><li data-v-070f2274>AGENT_EXEC_ERROR = 2</li><li data-v-070f2274>QUEUED_TASK_EXPIRED = 3</li><li data-v-070f2274>TARGET_AGENT_NOT_SPECIFIED = 4</li><li data-v-070f2274>MISSING_TARGET_TAGS = 5</li></ul><h4 data-v-070f2274><b data-v-070f2274>dateStarted</b></h4><p data-v-070f2274>The date and time when the <code data-v-070f2274>Task</code> started.</p><h4 data-v-070f2274><b data-v-070f2274>dateCompleted</b></h4><p data-v-070f2274>The date and time when the <code data-v-070f2274>Task</code> completed.</p><h4 data-v-070f2274><b data-v-070f2274>ipAddress</b></h4><p data-v-070f2274>The IP address of the Machine where the <code data-v-070f2274>Task</code> was executed.</p><h4 data-v-070f2274><b data-v-070f2274>machineId</b></h4><p data-v-070f2274>The ID of the Machine where the <code data-v-070f2274>Task</code> was executed.</p><h4 data-v-070f2274><b data-v-070f2274>artifactsDownloadedSize</b></h4><p data-v-070f2274>The total size of the Artifacts downloaded for this task, measured in bytes.</p><h4 data-v-070f2274><b data-v-070f2274>runtimeVars</b></h4><p data-v-070f2274>A list of key-value pair <code data-v-070f2274>runtime variables</code> generated by this <code data-v-070f2274>Task</code>. These variables can be consumed/modified by subsequent Tasks in the same <code data-v-070f2274>Job</code> workflow.</p><h3 data-v-070f2274>Actions</h3><h4 data-v-070f2274>interrupt</h4><p data-v-070f2274>Interrupt a running <code data-v-070f2274>Task</code>. <code data-v-070f2274>Task</code> status must be <code data-v-070f2274>RUNNING</code>. <code data-v-070f2274>taskoutcomeaction/interrupt/:taskOutcomeId</code></p><h4 data-v-070f2274>restart</h4><p data-v-070f2274>Restart a <code data-v-070f2274>Task</code>. <code data-v-070f2274>Task</code> status must be <code data-v-070f2274>INTERRUPTED</code> or <code data-v-070f2274>FAILED</code>. <code data-v-070f2274>taskoutcomeaction/restart/:taskOutcomeId</code></p><h4 data-v-070f2274>cancel</h4><p data-v-070f2274>Cancel a <code data-v-070f2274>Task</code>. <code data-v-070f2274>Task</code> status must be <code data-v-070f2274>RUNNING</code> or <code data-v-070f2274>INTERRUPTED</code>. <code data-v-070f2274>taskoutcomeaction/cancel/:taskOutcomeId</code></p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Get all TaskOutcome objects from a Task</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/taskOutcome?filter=_taskId%3D%3D[taskId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;_jobId&quot;:&quot;[jobId]&quot;,\n            &quot;_taskId&quot;:&quot;[taskId]&quot;,\n            &quot;dateStarted&quot;:&quot;[date]&quot;,\n            &quot;ipAddress&quot;:&quot;&quot;,\n            &quot;machineId&quot;:&quot;&quot;,\n            &quot;artifactsDownloadedSize&quot;:0,\n            &quot;target&quot;:4,\n            &quot;runtimeVars&quot;:{},\n            &quot;failureCode&quot;:1,\n            &quot;status&quot;:22,\n            &quot;id&quot;:&quot;[TaskOutcome id]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;TaskOutcome&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h2 id=\"teamvariable\" class=\"subtitle pb-2\" data-v-070f2274>TeamVariable</h2><p data-v-070f2274>TeamVariables have team-wide scope. They are useful for storing variables needed in Jobs across the team.</p><h3 data-v-070f2274>Reference</h3><div class=\"table-container\" data-v-070f2274><table class=\"table is-striped is-hoverable is-bordered\" data-v-070f2274><thead data-v-070f2274><tr data-v-070f2274><th data-v-070f2274>Attribute</th><th data-v-070f2274>GET</th><th data-v-070f2274>PUT</th><th data-v-070f2274>POST (required)</th><th data-v-070f2274>DELETE</th></tr></thead><tbody data-v-070f2274><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>id</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>_teamId</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td><td data-v-070f2274></td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>name</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr><tr data-v-070f2274><td data-v-070f2274><code data-v-070f2274>value</code></td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td class=\"has-text-primary has-background-primary-light\" data-v-070f2274>x</td><td data-v-070f2274></td></tr></tbody></table></div><h3 data-v-070f2274>Attribute Descriptions</h3><h4 data-v-070f2274><b data-v-070f2274>id</b></h4><p data-v-070f2274>Unique ID assigned when a new <code data-v-070f2274>TeamVariable</code> object is created.</p><h4 data-v-070f2274><b data-v-070f2274>_teamId</b></h4><p data-v-070f2274>The ID of the Team object where the <code data-v-070f2274>TeamVariable</code> is created.</p><h4 data-v-070f2274><b data-v-070f2274>name</b></h4><p data-v-070f2274>The <code data-v-070f2274>TeamVariable</code> key.</p><h4 data-v-070f2274><b data-v-070f2274>value</b></h4><p data-v-070f2274>The <code data-v-070f2274>TeamVariable</code> value.</p><h3 data-v-070f2274>Sample Code</h3><h4 data-v-070f2274>Create a new TeamVariable</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request POST &#39;https://saasglue.com/api/v0/teamvar&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --header &#39;Content-Type: text/plain&#39; \\ --data-raw &#39;{ &quot;name&quot; : &quot;[key]]&quot;, &quot;value&quot; : &quot;[value]]&quot; }&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:201,\n    &quot;data&quot;:{\n        &quot;name&quot;:&quot;[key]&quot;,\n        &quot;value&quot;:&quot;[value]&quot;,\n        &quot;_teamId&quot;:&quot;[teamId]&quot;,\n        &quot;id&quot;:&quot;[TeamVariable ID]&quot;,\n        &quot;version&quot;:0,\n        &quot;type&quot;:&quot;TeamVariable&quot;\n    }\n}\n                    </pre><h4 data-v-070f2274>Get all TeamVariables</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request GET &#39;https://saasglue.com/api/v0/teamvar&#39; --header &#39;Content-Type: application/json&#39; --header &#39;Auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:[\n        {\n            &quot;_teamId&quot;:&quot;[teamId]&quot;,\n            &quot;name&quot;:&quot;[name]&quot;,\n            &quot;value&quot;:&quot;[value]&quot;,\n            &quot;id&quot;:&quot;[TeamVariable ID]&quot;,\n            &quot;version&quot;:0,\n            &quot;type&quot;:&quot;TeamVariable&quot;\n        }\n    ],\n    &quot;meta&quot;:{\n        &quot;count&quot;:1\n    }\n}\n                    </pre><h4 data-v-070f2274>Delete an TeamVariable</h4><p data-v-070f2274>Request</p><pre data-v-070f2274>curl --location --request DELETE &#39;https://saasglue.com/api/v0/teamvar/[teamVariableId]&#39; --header &#39;Content-Type: application/json&#39; --header &#39;auth: [auth token]&#39; --header &#39;_teamId: [teamId]&#39; --data-raw &#39;&#39;</pre><p data-v-070f2274>Response</p><pre data-v-070f2274>{\n    &quot;statusCode&quot;:200,\n    &quot;data&quot;:{\n        &quot;n&quot;:1,\n        &quot;ok&quot;:1,\n        &quot;deletedCount&quot;:1\n    }\n}\n                    </pre></div>", 1)
